import "./Review.css";
import React from "react";
import {
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Box,
} from "@material-ui/core";
import StarIcon from "@material-ui/icons/Star";

function DrowRating(props) {
  return (
    <TableContainer>
      <Table size="small" aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Over All Rating</TableCell>
            <TableCell className="tabclass">
              <Box display="flex" flexDirection="row">
                <Box>
                  <StarIcon className="starclass" />
                </Box>
                <Box>{props.review.rating}/5</Box>
              </Box>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell>Cleanliness:</TableCell>
            <TableCell className="tabclass">
              <Box display="flex" flexDirection="row">
                <Box>
                  <StarIcon className="starclass" />
                </Box>
                <Box>{props.review.cleanliness}/5</Box>
              </Box>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Service:</TableCell>
            <TableCell className="tabclass">
              <Box display="flex" flexDirection="row">
                <Box>
                  <StarIcon className="starclass" />
                </Box>
                <Box>{props.review.service}/5</Box>
              </Box>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Atmosphere:</TableCell>
            <TableCell className="tabclass">
              <Box display="flex" flexDirection="row">
                <Box>
                  <StarIcon className="starclass" />
                </Box>
                <Box>{props.review.ambiance}/5</Box>
              </Box>
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell>Price:</TableCell>
            <TableCell className="tabclass">
              <Box display="flex" flexDirection="row">
                <Box>
                  <StarIcon className="starclass" />
                </Box>
                <Box>{props.review.price}/5</Box>
              </Box>
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </TableContainer>
  );
}

export default DrowRating;
