import React, { Component } from "react";
import { connect } from "react-redux";
import { Button } from "@material-ui/core";
import { reviewReport } from "../../../../actions/report";

class ReportReview extends Component {
  onClick = () => {
    this.props.reviewReport(this.props.id);
  };
  drawReport = () => {
    if (this.props.user && this.props.user.usertype && this.props.user._id !== this.props.userid) {
      return (
        <Button
          variant="contained"
          size="small"
          color="primary"
          onClick={this.onClick}
        >
          Report Review
        </Button>
      );
    }
  };
  render() {
    return <>{this.drawReport()}</>;
  }
}
const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    user: state.profile.user,
  };
};

export default connect(mapStateToProps, { reviewReport })(ReportReview);
