import "./Addpost.css";
import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import {
  TextField,
  Button,
  Box,
  Paper,
  Typography,
  Grid,
  CardMedia,
  Tooltip,
} from "@material-ui/core";
import uploadImglogo from "../../../images/photo-album-icon-png-24.jpg";
import uploadVidlogo from "../../../images/video.jpg";

import { addpost } from "../../../actions/post";

class AddPost extends Component {
  state = {
    price: "",
    promo: "",
    description: "",
    geolocation: "",
    weblink: "",
    tempimg: [],
    images: [],
    tempvid: "",
    photos: [],
    videolink: "",
    vid: "",
    subIndex: null,
    country: "",
    city: "",
  };
  componentDidMount() {
    this.props.subs.forEach((sub, index) => {
      if (sub._id === this.props.subid) {
        this.setState({ subIndex: index });
      }
    });
  }
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  onFormSubmit = async (event) => {
    event.preventDefault();
    const {
      description,
      price,
      promo,
      videolink,
      photos,
      city,
      country,
      geolocation,
      weblink,
    } = this.state;
    this.props.addpost({
      description,
      price,
      promo,
      videolink,
      photos,
      city,
      country,
      geolocation,
      weblink,
    });
  };

  remove = (file) => {
    let arr = this.state.tempimg;
    let ar = this.state.photos;
    ar.splice(file, 1);
    arr.splice(file, 1);
    this.setState({
      tempimg: arr,
      photos: ar,
    });
    console.log(this.state.tempimg);
  };
  removevid = () => {
    this.setState({
      tempvid: "",
    });
  };
  setImage = async (event) => {
    // console.log( URL.createObjectURL(event.target.files[0]));
    // console.log(event.target.files[0])
    const temp1 = [];

    for (let i = 0; i < Object.keys(event.target.files).length; i++) {
      this.setState({
        tempimg: [
          ...this.state.tempimg,
          await URL.createObjectURL(event.target.files[i]),
        ],
      });
      temp1[i] = event.target.files[i];
    }
    await this.setState({
      images: event,
    });
    console.log("tempimg=> ", this.state.tempimg);
    this.renderimg();
  };
  setVideo = async (event) => {
    if (event.target.files[0]) {
      let file = event.target.files[0];
      let blobURL = URL.createObjectURL(file);
      await this.setState({
        tempvid: blobURL,
        vid: event,
      });
    }
    console.log("tempvid=>", this.state.tempvid);
  };
  uploadImage = async (event) => {
    var filearr = [];
    this.setImage(event);
    //console.log(Object.keys(file).length)
    for (
      let i = 0;
      i < Object.keys(event.target.files).length;
      i++ // Initial FormData
    ) {
      const formData = new FormData();
      formData.append("file", event.target.files[i]);
      formData.append("upload_preset", "xxhdspvy");
      const requestOptions = {
        method: "POST",
        body: formData,
      };
      fetch("https://api.cloudinary.com/v1_1/ded8dyfkt/upload", requestOptions)
        .then((response) => response.json())
        .then((json) => {
          filearr[i] = json.secure_url;
          console.log("coludinary=>", json.secure_url);
        });
    }
    this.setState({
      photos: filearr,
    });
  };
  uploadVideo = async (event) => {
    this.setVideo(event);
    const formData = new FormData();
    formData.append("file", event.target.files[0]);
    formData.append("upload_preset", "xxhdspvy");
    const requestOptions = {
      method: "POST",
      body: formData,
    };
    fetch("https://api.cloudinary.com/v1_1/ded8dyfkt/upload", requestOptions)
      .then((response) => response.json())
      .then((json) => {
        console.log(json.secure_url);
        this.setState({
          videolink: json.secure_url,
        });
        console.log("abay oo ", this.state.videolink);
      });
  };
  renderimg = () => {
    if (this.state.tempimg.length === 0 && this.state.tempvid === "") {
      return (
        <Grid
          container
          justifyContent="center"
          alignItems="center"
          className="uploadbox"
          spacing={3}
        >
          <Typography variant="h2" className="center" style={{ opacity: 0.3 }}>
            Add Files
          </Typography>
        </Grid>
      );
    } else if (this.state.tempimg.length > 0 && this.state.tempvid !== "") {
      return (
        <Grid
          justifyContent="center"
          alignItems="center"
          className="uploadbox"
          container
          spacing={3}
        >
          {this.state.tempimg.map((file, index) => (
            <Grid key={index} item xs={2} sm={2}>
              <CardMedia
                component="img"
                onClick={(event) => this.remove(index)}
                className="uploaded"
                image={file}
              />
            </Grid>
          ))}
          <Grid item xs={2} sm={2} justifyContent="center" alignItems="center">
            <video
              className="uploaded"
              src={this.state.tempvid}
              width="100"
              height="100"
              autoPlay
              loop
              onClick={this.removevid}
              muted
            >
              Your browser does not support the video tag.
            </video>
          </Grid>
        </Grid>
      );
    } else if (this.state.tempimg.length === 0 && this.state.tempvid !== "") {
      return (
        <Grid
          className="uploadbox"
          justifyContent="center"
          alignItems="center"
          container
          spacing={3}
        >
          <Grid justifyContent="center" alignItems="center" item xs={2} sm={2}>
            <video
              className="uploaded"
              src={this.state.tempvid}
              width="100"
              height="100"
              autoPlay
              loop
              onClick={this.removevid}
              muted
            >
              Your browser does not support the video tag.
            </video>
          </Grid>
        </Grid>
      );
    } else if (this.state.tempimg.length > 0 && this.state.tempvid === "") {
      return (
        <Grid
          justifyContent="center"
          alignItems="center"
          className="uploadbox"
          container
          spacing={3}
        >
          {this.state.tempimg.map((file, index) => (
            <Grid key={index} item xs={2} sm={2}>
              <CardMedia
                className="uploaded"
                image={file}
                onClick={(event) => this.remove(index)}
              />
            </Grid>
          ))}
        </Grid>
      );
    }
  };
  drawWeb = () => {
    if (this.state.subIndex) {
      if (this.props.subs[this.state.subIndex].weblink) {
        return (
          <Grid item xs={12} md={6}>
            <TextField
              name="weblink"
              label="Web Links"
              variant="outlined"
              margin="normal"
              color="secondary"
              onChange={(e) => this.onChange(e)}
              value={this.state.weblink}
              fullWidth
            />
          </Grid>
        );
      }
    }
    return <></>;
  };
  drawLoc = () => {
    if (this.state.subIndex) {
      if (this.props.subs[this.state.subIndex].geolocation) {
        return (
          <Grid item xs={12} md={6}>
            <TextField
              name="geolocation"
              label="Geolocation"
              variant="outlined"
              margin="normal"
              color="secondary"
              onChange={(e) => this.onChange(e)}
              value={this.state.geolocation}
              fullWidth
            />
          </Grid>
        );
      }
    }
    return <></>;
  };
  drawVid = () => {
    if (this.state.subIndex) {
      if (this.props.subs[this.state.subIndex].video) {
        return (
          <Tooltip title="Upload video">
            <Button
              component="label"
              className="uploadbtn"
              style={{
                backgroundImage: `url(${uploadVidlogo})`,
              }}
            >
              <input
                name="video"
                type="file"
                hidden
                accept="video/mp4,video/x-m4v,video/*"
                onChange={(e) => this.uploadVideo(e)}
              />
            </Button>
          </Tooltip>
        );
      }
    }
    return <></>;
  };
  render() {
    if (this.props.user && this.props.user.usertype !== "visitor") {
      return (
        <div className="main">
          <Paper variant="outlined" className="paperclass">
            <Typography variant="h3" className="center">
              Add New Post
            </Typography>
            <Box mb={1} />
            <form onSubmit={this.onFormSubmit}>
              {this.renderimg()}
              <Grid container justifyContent="center" item xs={12}>
                <Tooltip title="Upload image">
                  <Button
                    className="uploadbtn"
                    component="label"
                    style={{
                      backgroundImage: `url(${uploadImglogo})`,
                    }}
                  >
                    <input
                      name="image"
                      type="file"
                      hidden
                      multiple
                      accept="image/png, image/jpeg"
                      onChange={(e) => this.uploadImage(e)}
                    />
                  </Button>
                </Tooltip>
                {this.drawVid()}
              </Grid>

              {/* Price and promo */}
              <Grid container spacing={1}>
                <Grid item xs={12} md={6}>
                  <TextField
                    name="price"
                    label="Price"
                    placeholder="Enter price range"
                    variant="outlined"
                    margin="normal"
                    color="secondary"
                    onChange={(e) => this.onChange(e)}
                    value={this.state.price}
                    required
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    name="promo"
                    label="Promo Code"
                    variant="outlined"
                    placeholder="Enter promo code if there is any"
                    margin="normal"
                    color="secondary"
                    onChange={(e) => this.onChange(e)}
                    value={this.state.promo}
                    fullWidth
                  />
                </Grid>
              </Grid>
              {/* City Country */}
              <Grid container spacing={1}>
                <Grid item xs={12} md={6}>
                  <TextField
                    name="city"
                    label="City"
                    variant="outlined"
                    margin="normal"
                    color="secondary"
                    onChange={(e) => this.onChange(e)}
                    value={this.state.city}
                    required
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12} md={6}>
                  <TextField
                    name="country"
                    label="State or Country"
                    variant="outlined"
                    margin="normal"
                    color="secondary"
                    onChange={(e) => this.onChange(e)}
                    value={this.state.country}
                    required
                    fullWidth
                  />
                </Grid>
              </Grid>

              {/* Discription */}
              <Grid container spacing={1}>
                <Grid item xs={12}>
                  <TextField
                    multiline
                    minRows={3}
                    name="description"
                    label="Description"
                    type="text"
                    variant="outlined"
                    margin="normal"
                    color="secondary"
                    onChange={(e) => this.onChange(e)}
                    value={this.state.description}
                    required
                    fullWidth
                  />
                </Grid>
              </Grid>
              {/* Location and weblink */}
              <Grid container spacing={1}>
                {this.drawLoc()}
                {this.drawWeb()}
              </Grid>
              <Box mb={2} />
              <Grid container spacing={1}>
                <Grid item xs={12} md={6}>
                  <Button
                    variant="contained"
                    color="secondary"
                    type="submit"
                    size="large"
                    fullWidth
                  >
                    Add Post
                  </Button>
                </Grid>
                <Grid item xs={12} md={6}>
                  <Link to="/profile" className="link-class">
                    <Button
                      variant="outlined"
                      color="secondary"
                      size="large"
                      fullWidth
                    >
                      Cancel
                    </Button>
                  </Link>
                </Grid>
              </Grid>
            </form>
          </Paper>
          <Box mb={3} />
        </div>
      );
    } else {
      return <Redirect to="/updateprofile" />;
    }
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    user: state.profile.user,
    subs: state.subs.subs,
    subid: state.subs.usersub,
  };
};

export default connect(mapStateToProps, { addpost })(
  withTranslation()(AddPost)
);
