import "./subs.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Card,
  CardContent,
  Typography,
  Divider,
  Grid,
  Container,
  Button,
  List,
  ListItem,
  ListItemText,
  ListItemIcon,
} from "@material-ui/core";
import DoneOutlineIcon from "@material-ui/icons/DoneOutline";
import StripeCheckout from "react-stripe-checkout";
import SubscriptionsIcon from "@material-ui/icons/Subscriptions";

import { uploadsubs } from "../../actions/Subs";

export class SubsCard extends Component {
  makePay = () => {
    this.props.uploadsubs(this.props.sub._id);
  };
  render() {
    return (
      <div>
        <Card variant="outlined" className="subcard" style={
          {
            backgroundColor: this.props.color
          }
        }>
          <Container className="cardtitle">
            <Typography variant="h4" className="offerlabel">
              {this.props.sub.name}  $
              {this.props.sub.price}
            </Typography>
          </Container>
          <CardContent>
            <List component="nav" aria-label="contacts">
              <ListItem>
                <ListItemIcon>
                  <DoneOutlineIcon />
                </ListItemIcon>
                <ListItemText primary={this.props.sub.description} />
              </ListItem>

              <ListItem>
                <ListItemIcon>
                  <DoneOutlineIcon />
                </ListItemIcon>
                <ListItemText
                  primary={"Upto " + this.props.sub.noofpost + " of Posts "}
                />
              </ListItem>

              <ListItem>
                <ListItemIcon>
                  <DoneOutlineIcon />
                </ListItemIcon>
                <ListItemText
                  primary={"Upload upto " + this.props.sub.image + " image/s"}
                />
              </ListItem>

              <ListItem>
                <ListItemIcon>
                  <DoneOutlineIcon />
                </ListItemIcon>
                <ListItemText
                  primary={
                    "Upto " + this.props.sub.validaty + " days of validity "
                  }
                />
              </ListItem>

              {this.props.sub.video === true ? (
                <ListItem>
                  <ListItemIcon>
                    <DoneOutlineIcon />
                  </ListItemIcon>
                  <ListItemText primary="Upload Videos" />
                </ListItem>
              ) : null}

              {this.props.sub.websitelink === true ? (
                <ListItem>
                  <ListItemIcon>
                    <DoneOutlineIcon />
                  </ListItemIcon>
                  <ListItemText primary="Upload Website Links" />
                </ListItem>
              ) : null}

              {this.props.sub.geolocation === true ? (
                <ListItem>
                  <ListItemIcon>
                    <DoneOutlineIcon />
                  </ListItemIcon>
                  <ListItemText primary="Upload Your Geolocation" />
                </ListItem>
              ) : null}
            </List>
          </CardContent>
          <Divider />
          <Grid
            container
            direction="row"
            justifyContent="space-evenly"
            alignItems="center">
            <div className="cardfooter">
              <StripeCheckout
                stripeKey="pk_test_51JNDqqCapusm4sMe58dx1nCkvNiNeJc84O7lR4kq5fISJvTUAteauihpF83lIkgPN5FmTXygEEZOPfct89hlR43A008XZgqIxT"
                token={this.makePay}
                name="Subscribe">
                <Button variant="outlined" style={{backgroundColor:"white"}}>
                  <SubscriptionsIcon className="likecolor" />
                  <Typography style={{ marginLeft: 6 }}>Subscribe</Typography>
                </Button>
              </StripeCheckout>
            </div>
          </Grid>
        </Card>
      </div>
    );
  }
}

export default connect(null, { uploadsubs })(SubsCard);
