import axios from "axios";
import { GET_PROFILE, GET_QNA, UPDATE_AVATAR, GET_WORKINGHOURS } from "./types";
import { baseURL, config } from "../apis/api";
import { setAlert } from "./alert";

export const getProfile = () => {
  return async (dispatch) => {
    try {
      const res = await axios.get(baseURL + "profile/getprofile");
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      } else {
        dispatch({ type: GET_PROFILE, payload: res.data.profile });
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

export const getWorkingHours = () => {
  return async (dispatch) => {
    try {
      const res = await axios.get(baseURL + "profile/getworkinghours");
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      } else {
        dispatch({
          type: GET_WORKINGHOURS,
          payload: res.data.workingHours.workingHours,
        });
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

export const updateProfile = (profile) => {
  return async (dispatch) => {
    const body = JSON.stringify(profile);
    try {
      const res = await axios.post(
        baseURL + "profile/updateprofile",
        body,
        config
      );

      if (res.data.msg.length) {
        dispatch(setAlert(res.data.msg[0], "success"));
      }
      if (res.data.error.length) {
        dispatch(setAlert(res.data.error[0], "error"));
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

