import "./Auth.css";
import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { baseURL } from "../../apis/api";
import {
  TextField,
  Button,
  Box,
  Paper,
  Typography,
  Grid,
  Link,
} from "@material-ui/core";

import { login } from "../../actions/auth";
import { google } from "../../actions/auth";

class Login extends Component {
  state = { email: "", password: "" };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  googleLogin = () => {
    this.props.google();
  };
  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.login(this.state);
  };

  render() {
    if (this.props.isSignedIn) {
      return <Redirect to="/updateprofile" />;
    }
    return (
      <div className="loginform login-w">
        <Paper variant="outlined" className="paperclass">
          <Typography variant="h4" className="center">
            Login
          </Typography>

          <Box mb={1} />
          <form onSubmit={this.onFormSubmit}>
            <TextField
              name="email"
              label="Email"
              type="email"
              variant="outlined"
              margin="normal"
              color="secondary"
              onChange={(e) => this.onChange(e)}
              value={this.state.email}
              required
              fullWidth
              autoFocus
            />
            <TextField
              name="password"
              label="Password"
              variant="outlined"
              margin="normal"
              type="password"
              color="secondary"
              onChange={(e) => this.onChange(e)}
              value={this.state.password}
              required
              fullWidth
            />
            <Box mb={2} />
            <Button
              variant="contained"
              color="secondary"
              size="large"
              type="submit"
              fullWidth
            >
              Login
            </Button>
          </form>
          <Box mb={3} />
          <Typography color="textSecondary" className="center divtextcen">
            <span>or continue with</span>
          </Typography>
          <Box mb={3} />
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <Link
                onClick={(e) => e.preventDefault}
                href={baseURL + "user/auth/google"}
              >
                <Button
                  variant="contained"
                  className="google-btn"
                  size="large"
                  onClick={this.googleLogin}
                  fullWidth
                >
                  Google
                </Button>
              </Link>
            </Grid>
            <Grid item xs={12} md={6}>
              <Link
                onClick={(e) => e.preventDefault}
                href={baseURL + "user/auth/facebook"}
              >
                <Button
                  variant="contained"
                  className="facebook-btn"
                  size="large"
                  fullWidth
                >
                  Facebook
                </Button>
              </Link>
            </Grid>
          </Grid>
        </Paper>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn };
};

export default connect(mapStateToProps, { google, login })(Login);
