import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import { withTranslation } from "react-i18next";

import { Box, Paper, Grid } from "@material-ui/core";
// components
import PostCard from "./PostCard";
import HeaderInfo from "./HeaderInfo";
import DisplayWorkingHour from "./DisplayWorkingHour";
import DisplayQNA from "./DisplayQNA";

import { companywh } from "../../../actions/profile";

class VisitorView extends Component {
  componentDidMount() {
    this.props.companywh(
      this.props.posts[this.props.match.params.index].User.workingHours
    );
  }
  ifpost = () => {
    if (this.props.posts) {
      return this.props.posts.map((post) => {
        if (
          this.props.posts[this.props.match.params.index].User._id ===
          post.User._id
        ) {
          return (
            <Grid item key={post.id} xs={12} md={6} lg={3}>
              <PostCard post={post} />
            </Grid>
          );
        }
        return <></>;
      });
    } else {
      return <></>;
    }
  };
  drowPosts = () => {
    return (
      <>
        <Box mb={3} />
        <DisplayWorkingHour />
        <Box mb={3} />
        <DisplayQNA
          user={this.props.posts[this.props.match.params.index].User}
          visitor={this.props.user}
        />
        <Box mb={3} />
        <Grid container spacing={2}>
          {this.ifpost()}
        </Grid>
      </>
    );
  };
  render() {
    if (this.props.user && this.props.user.usertype) {
      return (
        <div className="divcenter companydetailscard">
          <Paper container="true" variant="outlined" className="paperclass">
            <HeaderInfo
              userProp={this.props.posts[this.props.match.params.index].User}
              usertype={this.props.user.usertype}
            />
          </Paper>
          {this.drowPosts()}
          <Box mb={3} />
        </div>
      );
    } else {
      return <Redirect to="/updateprofile" />;
    }
  }
}

const mapStateToProps = (state) => {
  return { posts: state.posts.posts, user: state.profile.user };
};
export default connect(mapStateToProps, { companywh })(
  withTranslation()(VisitorView)
);
