import "./Index.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import { Box, CardMedia, Grid, Typography } from "@material-ui/core";
import { getallPosts } from "../../actions/post";
import PostCard from "../Company/Home/PostCard";
import top from "../../images/top.PNG";

class CardSection extends Component {
  state = { img: top };
  componentDidMount() {
    this.props.getallPosts();
  }

  renderposts = () => {
    if (this.props.posts) {
      return (
        <Grid container spacing={2}>
          {this.props.posts.slice(0, 4).map((post) => (
            <Grid item key={post.id} xs={12} md={6} lg={3}>
              <PostCard post={post} />
            </Grid>
          ))}
        </Grid>
      );
    } else {
      return <Box />;
    }
  };
  render() {
    return (
      <div className="divcenter companydetailscard">
        <Box mb={3} />
        <Grid
          xs={12}
          direction="column"
          container
          alignItems="center"
          justifyContent="center"
          style={{ marginTop: 40 , marginBottom:50}}>
          <CardMedia image={top} style={{ height: 100, width: 100 }} />
          <Typography variant="h4">Top Featured</Typography>
          <Typography variant="h6" className="subtext">
            Recent announcements
          </Typography>
        </Grid>
        {this.renderposts()}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { posts: state.posts.posts };
};
export default connect(mapStateToProps, { getallPosts })(
  withTranslation()(CardSection)
);
