import {
  REGISTER_FAIL,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  GOOGLE_LOGIN,
  // EDIT_PROFILE,
} from "../actions/types";

const INITIAL_STATE = {
  isSignedIn: false,
  loading: true,
};

const authReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case LOGIN_SUCCESS:
    case GOOGLE_LOGIN:
      return { ...state, isSignedIn: true, loading: false };

    case REGISTER_FAIL:
    case LOGIN_FAIL:
      return { ...state, loading: false };

    case LOGOUT:
      return {
        ...state,
        isSignedIn: false,
        loading: false,
      };

    default:
      return state;
  }
};

export default authReducer;
