import "./Profile.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import Autocomplete from "@material-ui/lab/Autocomplete";
import DeleteIcon from "@material-ui/icons/Delete";
import AddIcon from "@material-ui/icons/Add";
import { withTranslation } from "react-i18next";

import {
  Paper,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  TableContainer,
  Box,
  Button,
  TextField,
  IconButton,
} from "@material-ui/core";

import { updateWorkingHours, getWorkingHours } from "../../../actions/profile";

class WorkingHours extends Component {
  state = {
    rows: [],
    options: [],
    day: "",
    start: "",
    end: "",
    startTime: [],
    endTime: [],
  };
  componentDidMount() {
    const { t } = this.props;
    const temp = [
      t("Monday"),
      t("Tuesday"),
      t("Wednesday"),
      t("Thursday"),
      t("Friday"),
      t("Saturday"),
      t("Sunday"),
    ];
    this.firstday(temp);
    var time = Array.from(new Array(24 * 2)).map(
      (_, index) =>
        `${index < 20 ? "0" : ""}${Math.floor(index / 2)}:${
          index % 2 === 0 ? "00" : "30"
        }`
    );
    this.setState({ startTime: time });
  }
  firstday = async (temp) => {
    await this.props.getWorkingHours();
    await this.setState({ options: temp });

    if(this.props.workingHours.length > 0) {
      this.props.workingHours.forEach(async (wh) => {
        await this.setState({
          rows: [
            ...this.state.rows,
            {
              day: Object.keys(wh)[0],
              start: wh[Object.keys(wh)][0],
              end: wh[Object.keys(wh)][1],
            },
          ],
          options: this.state.options.filter(
            (option) => option !== Object.keys(wh)[0]
          ),
        });
      });
    }
    
    await this.setState({
      day: this.state.options[0],
      start: "09:00",
      end: "23:00",
    });
  };
  onAddClick = async () => {
    const { t } = this.props;
    const list = [
      t("Monday"),
      t("Tuesday"),
      t("Wednesday"),
      t("Thursday"),
      t("Friday"),
      t("Saturday"),
      t("Sunday"),
    ];
    if (this.state.day && this.state.start && this.state.end) {
      await this.setState({
        rows: [
          ...this.state.rows,
          { day: this.state.day, start: this.state.start, end: this.state.end },
        ],
        options: this.state.options.filter(
          (option) => option !== this.state.day
        ),
      });

      await this.setState({
        rows: this.state.rows.sort((a, b) => {
          return list.indexOf(a.day) - list.indexOf(b.day);
        }),
      });

      if (this.state.options.length) {
        await this.setState({ day: this.state.options[0] });
      } else {
        await this.setState({ day: "", start: "", end: "" });
      }
    }
  };
  onFormSubmit = async (event) => {
    event.preventDefault();
    const temp = [];
    for (let i = 0; i < this.state.rows.length; i++) {
      let obj = {};
      obj[this.state.rows[i].day] = [
        this.state.rows[i].start,
        this.state.rows[i].end,
      ];
      temp.push(obj);
    }
    this.props.updateWorkingHours(temp);
  };
  ondayChange = (event, newvalues) => {
    if (newvalues) {
      this.setState({
        day: newvalues,
      });
    }
  };
  onstartChange = (event, newvalues) => {
    this.setState({
      start: newvalues,
    });
    var time = [];
    for (let i = this.state.startTime.indexOf(newvalues); i < 48; i++) {
      time[i] = this.state.startTime[i];
    }
    this.setState({ endTime: time });
  };
  onendChange = (event, newvalues) => {
    this.setState({
      end: newvalues,
    });
  };

  deleterow = async (day) => {
    const { t } = this.props;
    const list = [
      t("Monday"),
      t("Tuesday"),
      t("Wednesday"),
      t("Thursday"),
      t("Friday"),
      t("Saturday"),
      t("Sunday"),
    ];
    await this.setState({
      rows: this.state.rows.filter((row) => row.day !== day),
      options: [...this.state.options, day],
    });
    await this.setState({
      options: this.state.options.sort((a, b) => {
        return list.indexOf(a) - list.indexOf(b);
      }),
    });
    if (this.state.options.length) {
      await this.setState({
        day: this.state.options[0],
        start: "09:00",
        end: "23:00",
      });
    } else {
      await this.setState({ day: "", start: "", end: "" });
    }
  };

  render() {
    const { t } = this.props;
    return (
      <>
        <form onSubmit={this.onFormSubmit}>
          <Paper variant="outlined">
            <TableContainer>
              <Table size="small" aria-label="simple table">
                <TableHead>
                  <TableRow>
                    <TableCell>{t("days")}</TableCell>
                    <TableCell align="right">{t("start")}</TableCell>
                    <TableCell align="right">{t("end")}</TableCell>
                    <TableCell align="right"> </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {/* form */}
                  <TableRow>
                    {/* Days */}
                    <TableCell width="40%" className="dayclass">
                      <Autocomplete
                        value={this.state.day}
                        options={this.state.options}
                        onChange={this.ondayChange}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            size="small"
                            variant="outlined"
                            label={t("workh")}
                          />
                        )}
                      />
                    </TableCell>
                    {/* start time */}
                    <TableCell width="29%" align="right" className="startclass">
                      <Autocomplete
                        value={this.state.start}
                        options={this.state.startTime}
                        onChange={this.onstartChange}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            size="small"
                            variant="outlined"
                            label={t("start") + " " + t("time")}
                          />
                        )}
                      />
                    </TableCell>
                    {/* end time */}
                    <TableCell width="29%" align="right" className="endclass">
                      <Autocomplete
                        value={this.state.end}
                        options={this.state.endTime}
                        onChange={this.onendChange}
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            size="small"
                            variant="outlined"
                            label={t("end") + " " + t("time")}
                          />
                        )}
                      />
                    </TableCell>
                    {/* Add button */}
                    <TableCell width="2%" align="right" className="iconclass">
                      {this.state.options.length ? (
                        <IconButton
                          onClick={this.onAddClick}
                          aria-label="delete"
                        >
                          <AddIcon />
                        </IconButton>
                      ) : (
                        <p></p>
                      )}
                    </TableCell>
                  </TableRow>

                  {this.state.rows.map((row) => (
                    <TableRow key={row["day"]}>
                      <TableCell>{row["day"]}</TableCell>
                      <TableCell align="right">{row.start}</TableCell>
                      <TableCell align="right">{row.end}</TableCell>
                      <TableCell align="right">
                        <IconButton
                          className="iconclass"
                          onClick={() => this.deleterow(row["day"])}
                          aria-label="delete"
                        >
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  ))}
                  
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
          <Box mb={2} />
          <Button
            variant="contained"
            color="secondary"
            type="submit"
            size="large"
            fullWidth
          >
            {t("updhours")}
          </Button>
        </form>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    workingHours: state.profile.workingHours,
  };
};

export default connect(mapStateToProps, {
  updateWorkingHours,
  getWorkingHours,
})(withTranslation()(WorkingHours));
