import React, { Component } from "react";
import { connect } from "react-redux";
import { Typography, Button } from "@material-ui/core";
import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import FavoriteIcon from "@material-ui/icons/Favorite";
import { likereview, unlikereivew } from "../../../../actions/review";

class LikeReview extends Component {
  state = { liked: 0, likes: 0 };
  componentDidMount() {
    this.setState({ likes: this.props.likes.length });
    this.props.likes.forEach((id) => {
      if (this.props.user && this.props.user.usertype) {
        if (id === this.props.user._id) {
          this.setState({
            liked: 1,
          });
        }
      }
    });
  }
  like = () => {
    let temp = this.state.likes;
    if (this.state.liked === 1 && this.state.likes > 0) {
      --temp;
      this.setState({ liked: 0, likes: temp });
      this.props.unlikereivew(this.props.id);
    } else {
      ++temp;
      this.setState({ liked: 1, likes: temp });
      this.props.likereview(this.props.id);
    }
  };
  displaylikes = () => {
    if (this.state.liked) {
      return <FavoriteIcon className="likecolor" />;
    } else {
      return <FavoriteBorderIcon className="likecolor" />;
    }
  };
  likebtn = () => {
    if (this.props.isSignedIn) {
      return (
        <Button onClick={this.like}>
          {this.displaylikes()}
          <Typography>{this.state.likes}</Typography>
        </Button>
      );
    } else {
      return (
        <Button disabled>
          {this.displaylikes()}
          <Typography>{this.state.likes}</Typography>
        </Button>
      );
    }
  };
  render() {
    return <>{this.likebtn()}</>;
  }
}
const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    user: state.profile.user,
  };
};
export default connect(mapStateToProps, { likereview, unlikereivew })(
  LikeReview
);
