import "./CompanyHome.css";
import React, { Component } from "react";
import { connect } from "react-redux";

import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Avatar,
  Typography,
  Grid,
  Box,
} from "@material-ui/core";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import DrowAnswer from "./DrowAnswer";
import Reply from "./Reply";

import { getqna } from "../../../actions/profile";
import Question from "./Question";

class DisplayQNA extends Component {
  componentDidMount() {
    if (this.props.user.usertype !== "visitor") {
      this.props.getqna(this.props.user._id);
    }
  }
  regetqna = async () => {
    await this.props.getqna(this.props.user._id);
  };
  drowqora = (qa) => {
    if (!this.props.visitor) {
      return (
        <Box display="flex" flexDirection="row" className="replyclass">
          <Avatar
            alt="Visitor pic"
            src={this.props.user.avatar}
            className="avatarsize"
          />
          <Reply
            user={this.props.user}
            quesid={qa._id}
            regetqna={this.regetqna}
          />
        </Box>
      );
    } else {
      return <></>;
    }
  };
  drowAnswer = (qa) => {
    if (qa.answer) {
      return (
        <DrowAnswer qa={qa} user={this.props.user} regetqna={this.regetqna} />
      );
    } else {
      return this.drowqora(qa);
    }
  };
  dorwqinpu = () => {
    if (this.props.visitor) {
      return (
        <>
          <AccordionDetails>
            <Avatar alt="Visitor pic" src={this.props.visitor.avatar} />
            <Question
              user={this.props.visitor}
              compid={this.props.user._id}
              regetqna={this.regetqna}
            />
          </AccordionDetails>
        </>
      );
    }
  };
  drowQuestion = () => {
    if (this.props.qna && this.props.qna.length) {
      return (
        <>
          {this.props.qna.map((qa) => {
            return (
              <AccordionDetails key={qa.question}>
                <Avatar alt="Visitor pic" src={qa.User.avatar} />
                {/* Question section */}
                <Grid
                  container
                  direction="column"
                  justifyContent="flex-start"
                  alignItems="flex-start"
                  className="questionclass"
                >
                  {/* Visitor name */}
                  <Grid item>
                    <Typography>
                      <b>
                        {qa.User.fname} {qa.User.lname}
                      </b>
                    </Typography>
                  </Grid>
                  {/* Visitor question */}
                  <Grid item>
                    <Typography>{qa.question}</Typography>
                  </Grid>
                  {/* Anser section */}
                  <Grid item className="fullsize">
                    {this.drowAnswer(qa)}
                  </Grid>
                </Grid>
              </AccordionDetails>
            );
          })}
        </>
      );
    } else {
      return (
        <AccordionDetails>
          <Typography>Ask me a question!</Typography>
        </AccordionDetails>
      );
    }
  };
  render() {
    return (
      <div>
        <Accordion variant="outlined">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>QnA</Typography>
          </AccordionSummary>
          {this.drowQuestion()}
          {this.dorwqinpu()}
        </Accordion>
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  return { qna: state.profile.qna };
};

export default connect(mapStateToProps, { getqna })(DisplayQNA);
