import axios from "axios";
import { GET_SUB, GET_SUBID } from "./types";
import { baseURL, config } from "../apis/api";
import { setAlert } from "./alert";

export const getSubs = () => {
  return async (dispatch) => {
    try {
      const res = await axios.get(baseURL + "subscription/getsubs");
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      } else {
        dispatch({ type: GET_SUB, payload: res.data.subs });
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

export const uploadsubs = (subid) => {
  return async (dispatch) => {
    try {
      const body = JSON.stringify({ subid });
      const res = await axios.post(
        baseURL + "subscription/payment",
        body,
        config
      );
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

export const getSubid = () => {
  return async (dispatch) => {
    try {
      const res = await axios.get(baseURL + "subscription/getsubid");
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      } else {
        dispatch({ type: GET_SUBID, payload: res.data.id });
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};
