import React, { Component } from "react";

import { Avatar, Typography, Grid, Box } from "@material-ui/core";

class DrowAnswer extends Component {
  render() {
    return (
      <>
        <Box display="flex" flexDirection="row" className="replyclass">
          <Avatar
            alt="Visitor pic"
            src={this.props.user.avatar}
            className="avatarsize"
          />
          <Grid
            container
            direction="column"
            justifyContent="flex-start"
            alignItems="flex-start"
            className="answerclass"
          >
            {/* company User name */}
            <Grid item>
              <Typography>
                <b>
                  {this.props.user.fname} {this.props.user.lname}
                </b>
              </Typography>
            </Grid>
            {/* company User anser */}
            <Grid item>
              <Typography>{this.props.qa.answer}</Typography>
            </Grid>
          </Grid>
        </Box>
      </>
    );
  }
}

export default DrowAnswer;
