import "./Review.css";
import React, { Component } from "react";
import {
  Typography,
  Grid,
  Paper,
  Avatar,
  CardMedia,
  Box,
  Divider,
} from "@material-ui/core";

import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import DrowRating from "./DrowRating";
import LikeReview from "./LikeReview";
import PinReview from "./PinReview";
import ReportReview from "./ReportReview";

class DisplayReview extends Component {
  drowAgency = (review) => {
    if (review.User) {
      return (
        <>
          <Avatar
            className="profreview"
            alt="Visitor pic"
            src={review.User.avatar}
          />
          <Typography color="textSecondary">
            {review.User.fname} {review.User.lname}
          </Typography>
          <Box mb={1} />
          <Grid container spacing={1}>
            <Grid
              item
              xs={12}
              container
              justifyContent="center"
              alignItems="center"
            >
              <LikeReview likes={review.likes} id={review._id} />
            </Grid>
            <Grid
              item
              xs={12}
              container
              justifyContent="center"
              alignItems="center"
            >
              <PinReview pined={review.pined} id={review._id} />
            </Grid>
            <Grid
              item
              xs={12}
              container
              justifyContent="center"
              alignItems="center"
            >
              <ReportReview id={review._id} userid={review.User._id} />
            </Grid>
          </Grid>

          <Box mb={1} />
        </>
      );
    } else {
      return <></>;
    }
  };
  renderimg = (img) => {
    if (img) {
      return (
        <CardMedia
          varient="outlined"
          style={{ height: 150, width: 150 }}
          image={img}
        />
      );
    } else {
      return <></>;
    }
  };
  render() {
    return (
      <>
        {this.props.reviews.map((review) => (
          <>
            <Paper variant="outlined" className="paddall">
              <Grid container>
                {/* review user pic and name */}
                <Grid
                  item
                  xs={12}
                  md={2}
                  container
                  direction="column"
                  justifyContent="flex-start"
                  alignItems="center"
                >
                  {this.drowAgency(review)}
                </Grid>
                {/* rating */}
                <Grid item xs={12} md={3}>
                  <Box p={1}>
                    <Divider />
                    <DrowRating review={review} />
                  </Box>
                </Grid>
                {/* review det and pic */}
                <Grid item xs={12} md={6}>
                  <div className="paddall">
                    <Typography variant="h6" multiline fullWidth>
                      {review.title}
                    </Typography>
                    <Typography multiline fullWidth>
                      {review.text}
                    </Typography>
                    {this.renderimg(review.image)}
                  </div>
                </Grid>
              </Grid>
            </Paper>
            <Box mb={2} />
          </>
        ))}
        <Box mb={5} />
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn };
};

export default connect(mapStateToProps)(withTranslation()(DisplayReview));
