import "./Profile.css";
import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import {
  Box,
  Paper,
  Typography,
  Grid,
  Divider,
  Card,
  CardMedia,
  CircularProgress,
} from "@material-ui/core";
import EditProfile from "./EditProfile";
import { uploadAvatar } from "../../../actions/profile";
import { getSubs } from "../../../actions/Subs";
import WorkingHours from "./WorkingHours";

class Profile extends Component {
  state = { visitor: true, isHover: false, upload: "https://t.ly/IuEL" };
  componentDidMount() {
    this.props.getSubs();
  }

  setIsHover = (a) => {
    this.setState({ isHover: a });
  };

  isVisitor = async (utype) => {
    if (utype === "visitor") {
      await this.setState({ visitor: true });
    } else {
      await this.setState({ visitor: false });
    }
  };
  getAvatar = () => {
    if (this.props.user) {
      return this.props.user.avatar;
    } else {
      return "";
    }
  };
  getEmail = () => {
    if (this.props.user) {
      return this.props.user.email;
    } else {
      return "";
    }
  };

  uploadImage = (e) => {
    const formData = new FormData();
    formData.append("file", e[0]);
    formData.append("upload_preset", "xxhdspvy");
    const requestOptions = {
      method: "POST",
      body: formData,
    };
    fetch("https://api.cloudinary.com/v1_1/ded8dyfkt/upload", requestOptions)
      .then((response) => response.json())
      .then((json) => {
        const url = json.secure_url;
        console.log(url);
        this.props.uploadAvatar(url);
      });
  };
  drowWorkingHours = () => {
    const { t } = this.props;
    if (this.state.visitor) {
      return <></>;
    } else {
      return (
        <Grid item xs={12} lg={6}>
          <Box mb={2} />
          <Typography variant="h4" align="center">
            {t("workh")}
          </Typography>
          <Box mb={2} />
          <WorkingHours />
        </Grid>
      );
    }
  };

  render() {
    const { t } = this.props;
    if (!this.props.isSignedIn) {
      return <Redirect to="/login" />;
    }
    return (
      <div className="divcenter mt-w">
        <Paper variant="outlined" className="paperclass">
          <Grid
            container
            direction="column"
            justifyContent="center"
            alignItems="center"
          >
            {/* Avatar and email code */}

            <Card
              onMouseEnter={() => this.setIsHover(true)}
              onMouseLeave={() => this.setIsHover(false)}
              component="label"
              className="avatarcard"
            >
              {this.getAvatar() ? (
                <CardMedia
                  image={
                    this.state.isHover ? this.state.upload : this.getAvatar()
                  }
                  className="avatarclass"
                />
              ) : (
                <CircularProgress />
              )}
              <input
                type="file"
                hidden
                accept="image/png, image/jpeg, video/mp4,video/x-m4v,video/*"
                onChange={(e) => this.uploadImage(e.target.files)}
              />
            </Card>
            {/* end card */}
            <Box mb={2} />
            <Typography>{this.getEmail()}</Typography>
          </Grid>

          <Box mb={2} />
          <Divider />
          <Grid container spacing={1}>
            {/* Edit Profile Form */}
            <Grid item xs={12} lg={this.state.visitor ? 12 : 6}>
              <Box mb={2} />
              <Typography variant="h4" align="center">
                {t("profile_detail")}
              </Typography>
              <EditProfile isVisitor={this.isVisitor} />
            </Grid>
            {/* Working Hours */}
            {this.drowWorkingHours()}
          </Grid>
        </Paper>
        <Box mb={3} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn, user: state.profile.user };
};
export default connect(mapStateToProps, { uploadAvatar, getSubs })(
  withTranslation()(Profile)
);
