import React, { Component } from "react";
import { connect } from "react-redux";
import { Grid, Box, TextField, Button } from "@material-ui/core";

import { postanswer } from "../../../actions/profile";

class Reply extends Component {
  state = { answer: "" };
  clearanswer = () => {
    this.setState({ answer: "" });
  };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  onFormSubmit = async (event) => {
    event.preventDefault();
    this.props.postanswer(this.state.answer, this.props.quesid);
    this.props.regetqna();
  };
  render() {
    return (
      <form onSubmit={this.onFormSubmit} className="fullsize">
        <TextField
          name="answer"
          color="secondary"
          className="fullsize answerclass"
          value={this.state.answer}
          onChange={(e) => this.onChange(e)}
          autoComplete="off"
          placeholder={`Replying publicaly as ${this.props.user.fname} ${this.props.user.lname}`}
        />
        <Box mb={1} />
        <Grid container direction="row" alignItems="center">
          <Button
            variant="contained"
            color="secondary"
            type="submit"
            className="answerclass"
          >
            Submit
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            onClick={this.clearanswer}
            className="answerclass"
          >
            Cancle
          </Button>
        </Grid>
      </form>
    );
  }
}

export default connect(null, { postanswer })(Reply);
