import "./CompanyHome.css";
import React, { Component } from "react";
import FavoriteIcon from "@material-ui/icons/Favorite";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import {
  Card,
  CardMedia,
  CardActionArea,
  CardContent,
  Typography,
  Divider,
  Grid,
  Box,
} from "@material-ui/core";
import { Link } from "react-router-dom";

class PostCard extends Component {
  cardimg = () => {
    if (this.props.post.photos.length > 0) {
      return (
        <CardMedia
          className="img"
          image={this.props.post.photos[0]}
          title="Paella dish"
        />
      );
    } else {
      return (
        <CardMedia
          className="img"
          image="https://as1.ftcdn.net/v2/jpg/00/36/94/26/1000_F_36942622_9SUXpSuE5JlfxLFKB1jHu5Z07eVIWQ2W.jpg"
          title="Paella dish"
        />
      );
    }
  };
  promo = () => {
    if (this.props.post.promo) {
      return (
        <>
          <Divider />
          <Box display="flex" justifyContent="center" p={1}>
            <Box>
              <Typography variant="body1" component="p">
                Promo: <b>{this.props.post.promo}</b>
              </Typography>
            </Box>
          </Box>
        </>
      );
    } else {
      return <></>;
    }
  };
  clicks = () => {
    if (this.props.usertype && this.props.usertype !== "visitor") {
      return (
        <>
          <Divider />
          <Box display="flex" justifyContent="center" p={1}>
            <Box>
              <Typography variant="body1" component="p">
                Clicks: <b>{this.props.post.clicks}</b>
              </Typography>
            </Box>
          </Box>
        </>
      );
    } else {
      return <></>;
    }
  };
  render() {
    return (
      <Card variant="outlined">
        <CardActionArea>
          <Link
            style={{ textDecoration: "none" }}
            to={`/viewpost/${this.props.post._id}`}
          >
            {this.cardimg()}
            <CardContent>
              <Typography variant="body2" color="textSecondary" component="p">
                {this.props.post.description.substring(0, 30)}...
              </Typography>
            </CardContent>
          </Link>
        </CardActionArea>
        <Divider />
        <Grid
          container
          direction="row"
          justifyContent="space-evenly"
          alignItems="center"
        >
          <div className="cardfooter">
            <FavoriteIcon className="likecolor" />
            <Typography>{this.props.post.likes.length}</Typography>
          </div>

          <Divider orientation="vertical" flexItem />
          <div className="cardfooter">
            <AttachMoneyIcon className="dollercolor" />
            <Typography>{this.props.post.price}</Typography>
          </div>
        </Grid>
        {this.clicks()}
        {this.promo()}
      </Card>
    );
  }
}

export default PostCard;
