import "../../Company/Home/CompanyHome.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import {
  Box,
  Paper,
  Grid,
  InputBase,
  IconButton,
  Divider,
  Typography,
  Tooltip,
} from "@material-ui/core";

import ClearRoundedIcon from "@material-ui/icons/ClearRounded";

import { getallPosts } from "../../../actions/post";
import { removeSearch } from "../../../actions/front";
import PostCard from "../../Company/Home/PostCard";

class VisitorHome extends Component {
  state = { city: "", company: "" };
  componentDidMount() {
    this.props.getallPosts();
    if (
      this.props.search &&
      (this.props.search.city || this.props.search.company)
    ) {
      this.setState({
        city: this.props.search.city,
        company: this.props.search.company,
      });
    }
    this.props.removeSearch();
  }
  clearCompany = () => {
    this.setState({ company: "" });
  };
  clearCity = () => {
    this.setState({ city: "" });
  };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  search1 = (posts) => {
    return posts
      .filter((val) => {
        if (this.state.company === "" && this.state.city === "") {
          return val;
        } else if (
          this.state.city === "" &&
          (val.User.usertype
            .toLowerCase()
            .includes(this.state.company.toLowerCase()) ||
            val.User.companyname
              .toLowerCase()
              .includes(this.state.company.toLowerCase()))
        ) {
          return val;
        } else if (
          this.state.company === "" &&
          (val.city.toLowerCase().includes(this.state.city.toLowerCase()) ||
            val.country.toLowerCase().includes(this.state.city.toLowerCase()))
        ) {
          return val;
        } else if (
          (val.User.usertype
            .toLowerCase()
            .includes(this.state.company.toLowerCase()) ||
            val.User.companyname
              .toLowerCase()
              .includes(this.state.company.toLowerCase())) &&
          (val.city.toLowerCase().includes(this.state.city.toLowerCase()) ||
            val.country.toLowerCase().includes(this.state.city.toLowerCase()))
        ) {
          return val;
        }
        return val;
      })
      .map((val, key) => {
        return (
          <Grid item key={val._id} xs={12} md={6} lg={3}>
            <PostCard post={val} />
          </Grid>
        );
      });
  };
  renderposts = () => {
    if (this.props.posts) {
      return (
        <Grid container spacing={2}>
          {this.search1(this.props.posts)}
        </Grid>
      );
    } else {
      return <Typography variant="h2">No post found</Typography>;
    }
  };
  render() {
    return (
      <div className="divcenter companydetailscard">
        <div>
          <Grid
            xs={12}
            container
            spacing={1}
            alignItems="center"
            justifyContent="center"
          >
            <Grid item xs={4}>
              <Paper className="pad">
                <Box display="flex" className="centerclass ">
                  <Box>
                    <Typography>
                      <b>What</b>
                    </Typography>
                  </Box>
                  <Box className="inputclass">
                    <Divider orientation="vertical" className="dividerclass" />
                  </Box>
                  <Box flexGrow={1} className="inputclass">
                    <InputBase
                      name="company"
                      value={this.state.company}
                      onChange={(e) => this.onChange(e)}
                      style={{ width: "100%" }}
                      autoComplete="off"
                      placeholder="Try travel agency, hotels"
                    />
                  </Box>
                  <Box pr={1}>
                    <Tooltip title="Clear">
                      <IconButton
                        onClick={this.clearCompany}
                        className="iconButtonclass"
                        aria-label="search"
                      >
                        <ClearRoundedIcon />
                      </IconButton>
                    </Tooltip>
                  </Box>
                </Box>
              </Paper>
            </Grid>
            <Grid item xs={4}>
              <Paper className="pad">
                <Box display="flex" className="centerclass ">
                  <Box>
                    <Typography>
                      <b>Where</b>
                    </Typography>
                  </Box>
                  <Box className="searchclass">
                    <Divider orientation="vertical" className="dividerclass" />
                  </Box>
                  <Box flexGrow={1} className="searchclass">
                    <InputBase
                      name="city"
                      value={this.state.city}
                      onChange={(e) => this.onChange(e)}
                      style={{ width: "100%" }}
                      autoComplete="off"
                      placeholder="Type city name"
                    />
                  </Box>
                  <Box pr={1}>
                    <Tooltip title="Clear">
                      <IconButton
                        onClick={this.clearCity}
                        className="iconButtonclass"
                        aria-label="search"
                      >
                        <ClearRoundedIcon />
                      </IconButton>
                    </Tooltip>
                  </Box>
                </Box>
              </Paper>
            </Grid>
          </Grid>
        </div>
        <Box mb={3} />
        {this.renderposts()}
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { posts: state.posts.posts, search: state.search.search };
};
export default connect(mapStateToProps, { getallPosts, removeSearch })(
  withTranslation()(VisitorHome)
);
