import "./Review.css";
import React, { Component } from "react";
import {
  Typography,
  AccordionDetails,
  Accordion,
  AccordionSummary,
  TextField,
  Button,
  Grid,
  Paper,
  CardMedia,
} from "@material-ui/core";
import Rating from "@material-ui/lab/Rating";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import RateReviewIcon from "@material-ui/icons/RateReview";
import AttachFileIcon from "@material-ui/icons/AttachFile";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import { uploadReview } from "../../../../actions/review";
import { Link } from "react-router-dom";
class Review extends Component {
  state = {
    rating: 0,
    cleanliness: 0,
    price: 0,
    ambiance: 0,
    service: 0,
    title: "",
    text: "",
    image: "",
    tempimg: "",
    imglink: "",
  };
  onFormSubmit = async (event) => {
    event.preventDefault();
    const {
      image,
      rating,
      cleanliness,
      service,
      ambiance,
      price,
      title,
      text,
    } = this.state;
    this.props.uploadReview(
      {
        image,
        rating,
        cleanliness,
        service,
        ambiance,
        price,
        title,
        text,
      },
      this.props.postid
    );
  };
  renderimg = () => {
    if (this.state.tempimg === "") {
      return (
        <Grid
          container
          justifyContent="center"
          alignItems="center"
          className="attach"
        >
          <Typography variant="h3" className="center" style={{ opacity: 0.3 }}>
            Upload Image
          </Typography>
        </Grid>
      );
    } else {
      return <CardMedia className="attach" image={this.state.tempimg} />;
    }
  };
  onChange = async (e) => {
    await this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  setImage = async (e) => {
    if (e[0]) {
      let blobURL = URL.createObjectURL(e[0]);
      await this.setState({
        tempimg: blobURL,
        imglink: e[0],
      });
    }
  };
  uploadImage = (e) => {
    this.setImage(e);
    const formData = new FormData();
    formData.append("file", e[0]);
    formData.append("upload_preset", "xxhdspvy");
    const requestOptions = {
      method: "POST",
      body: formData,
    };
    fetch("https://api.cloudinary.com/v1_1/ded8dyfkt/upload", requestOptions)
      .then((response) => response.json())
      .then((json) => {
        const url = json.secure_url;
        this.setState({
          image: url,
        });
      });
  };
  setClean = async (newValue) => {
    await this.setState({
      cleanliness: newValue,
    });
    this.setRating();
  };
  setAmb = async (newValue) => {
    await this.setState({
      ambiance: newValue,
    });
    this.setRating();
  };
  setPrice = async (newValue) => {
    await this.setState({
      price: newValue,
    });
    this.setRating();
  };
  setval = async (newValue) => {
    await this.setState({
      rating: newValue,
      price: newValue,
      service: newValue,
      ambiance: newValue,
      cleanliness: newValue,
    });
  };
  setService = async (newValue) => {
    await this.setState({
      service: newValue,
    });
    this.setRating();
  };
  setRating = () => {
    let num =
      this.state.service +
      this.state.price +
      this.state.ambiance +
      this.state.cleanliness;
    if (num) {
      let result = num / 4;
      this.setState({
        rating: result,
      });
    }
  };
  render() {
    return (
      <div>
        <Accordion variant="outlined">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <RateReviewIcon color="secondary" />
            <Typography style={{ marginLeft: "8px" }}>
              Write A Review
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <form onSubmit={this.onFormSubmit}>
              <Grid container>
                <Grid item xs={12} style={{ marginBottom: "10px" }}>
                  <Paper
                    elevation={0}
                    component="fieldset"
                    mb={3}
                    borderColor="transparent"
                  >
                    <Typography component="legend">Your Rating</Typography>
                    <Grid container spacing={3}>
                      <Grid item xs={6}>
                        <Rating
                          precision="0.5"
                          value={this.state.rating}
                          onChange={(event, newValue) => {
                            this.setval(newValue);
                          }}
                        />
                      </Grid>
                      <Grid item xs={6}>
                        {this.renderimg()}
                        <Button
                          variant="outlined"
                          component="label"
                          color="secondary"
                          style={{ cursor: "pointer" }}
                        >
                          <AttachFileIcon />
                          Attach File
                          <input
                            name="image"
                            type="file"
                            hidden
                            accept="image/png, image/jpeg"
                            onChange={(e) => this.uploadImage(e.target.files)}
                          />
                        </Button>
                      </Grid>
                    </Grid>
                  </Paper>
                </Grid>

                <Grid
                  item
                  xs={6}
                  container
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                >
                  <Typography component="legend">Cleanliness:</Typography>
                  <Rating
                    value={this.state.cleanliness}
                    onChange={(event, newValue) => {
                      this.setClean(newValue);
                    }}
                  />
                </Grid>

                <Grid
                  item
                  xs={6}
                  container
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                >
                  <Typography component="legend">Service:</Typography>
                  <Rating
                    value={this.state.service}
                    onChange={(event, newValue) => {
                      this.setService(newValue);
                    }}
                  />
                </Grid>

                <Grid
                  item
                  xs={6}
                  container
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                >
                  <Typography component="legend">Atmosphere:</Typography>
                  <Rating
                    value={this.state.ambiance}
                    onChange={(event, newValue) => {
                      this.setAmb(newValue);
                    }}
                  />
                </Grid>

                <Grid
                  item
                  xs={6}
                  container
                  direction="column"
                  justifyContent="center"
                  alignItems="center"
                >
                  <Typography component="legend">Price:</Typography>
                  <Rating
                    value={this.state.price}
                    onChange={(event, newValue) => {
                      this.setPrice(newValue);
                    }}
                  />
                </Grid>

                <Grid item xs={12}>
                  <TextField
                    name="title"
                    label="Title"
                    variant="outlined"
                    margin="normal"
                    color="secondary"
                    size="small"
                    placeholder="Type best Title"
                    autoComplete="off"
                    onChange={(e) => this.onChange(e)}
                    value={this.state.fname}
                    required
                    fullWidth
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    name="text"
                    label="Review"
                    variant="outlined"
                    margin="normal"
                    color="secondary"
                    size="small"
                    placeholder="Type best Title"
                    autoComplete="off"
                    rows={3}
                    onChange={(e) => this.onChange(e)}
                    value={this.state.text}
                    multiline
                    required
                    fullWidth
                  />
                </Grid>
                <Grid item xs={3}>
                  <Link to="/visitorhome" className="link-class">
                    <Button
                      variant="contained"
                      type="submit"
                      color="secondary"
                      fullWidth
                    >
                      Submit
                    </Button>
                  </Link>
                </Grid>
              </Grid>
            </form>
          </AccordionDetails>
        </Accordion>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn };
};

export default connect(mapStateToProps, { uploadReview })(
  withTranslation()(Review)
);
