import "./Profile.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import {
  TextField,
  Button,
  Box,
  Grid,
  Select,
  InputLabel,
  FormControl,
  MenuItem,
} from "@material-ui/core";

// import actions
import { getProfile, updateProfile } from "../../../actions/profile";

class EditProfile extends Component {
  state = {
    fname: "",
    lname: "",
    usertype: "",
    companyname: "",
    address: "",
    mobileno: "",
    lineno: "",
    description: "",
    ftime: true,
  };
  componentDidMount() {
    this.getDetails();
  }
  getDetails = async () => {
    await this.props.getProfile();
    if (this.props.user) {
      if (this.props.user.usertype) {
        await this.setState({
          fname: this.props.user.fname,
          lname: this.props.user.lname,
          usertype: this.props.user.usertype,
          companyname: this.props.user.companyname,
          address: this.props.user.address,
          mobileno: this.props.user.mobileno,
          lineno: this.props.user.lineno,
          description: this.props.user.description,
          ftime: false,
        });
      }
      await this.setState({
        fname: this.props.user.fname,
        lname: this.props.user.lname,
      });

      this.props.isVisitor(this.state.usertype);
    }
  };
  checkNum = (e) => {
    if (e.target.value > 47 || e.target.value < 58) {
      this.setState({ ...this.state, [e.target.name]: e.target.value });
    }
  };
  onChange = async (e) => {
    await this.setState({ ...this.state, [e.target.name]: e.target.value });
    this.props.isVisitor(this.state.usertype);
  };
  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.updateProfile(this.state);

    this.getDetails();
  };
  render() {
    const { t } = this.props;
    return (
      <>
        <form onSubmit={this.onFormSubmit}>
          {/* First last and agency name */}
          <Grid container spacing={1}>
            <Grid item xs={12} md={6}>
              <TextField
                name="fname"
                label={t("fname")}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                onChange={(e) => this.onChange(e)}
                value={this.state.fname}
                required
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                name="lname"
                label={t("lname")}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                onChange={(e) => this.onChange(e)}
                value={this.state.lname}
                required
                fullWidth
              />
            </Grid>
          </Grid>

          {/* User Type & Agency Name */}
          <Grid container spacing={1}>
            <Grid item xs={12} lg={6}>
              <FormControl
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                required
                fullWidth
                disabled={!this.state.ftime}>
                <InputLabel>{t("utype")}</InputLabel>
                <Select
                  name="usertype"
                  label="User Type"
                  value={this.state.usertype}
                  onChange={(e) => this.onChange(e)}>
                  <MenuItem value={"agency"}>{t("agency")}</MenuItem>
                  <MenuItem value={"hotel"}>{t("hotel")}</MenuItem>
                  <MenuItem value={"visitor"}>{t("visitor")}</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} lg={6}>
              <TextField
                name="companyname"
                label={this.state.usertype === "visitor" ? "" : t("cname")}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                onChange={(e) => this.onChange(e)}
                value={this.state.companyname}
                required={this.state.usertype === "visitor" ? false : true}
                fullWidth
                disabled={this.state.usertype === "visitor" ? true : false}
              />
            </Grid>
          </Grid>
          {/* Address field */}
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <TextField
                name="address"
                label={t("Add")}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                onChange={(e) => this.onChange(e)}
                value={this.state.address}
                required
                fullWidth
              />
            </Grid>
          </Grid>

          {/* Number */}
          <Grid container spacing={1}>
            <Grid item xs={12} md={6}>
              <TextField
                name="mobileno"
                label={t("mnum")}
                type="text"
                onChange={(e) => this.checkNum(e)}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                value={this.state.mobileno}
                required
                fullWidth
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <TextField
                name="lineno"
                label={t("lnum")}
                type="text"
                onChange={(e) => this.checkNum(e)}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                value={this.state.lineno}
                fullWidth
              />
            </Grid>
          </Grid>

          {/* Description field */}
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <TextField
                name="description"
                label={t("desc")}
                variant="outlined"
                margin="normal"
                color="secondary"
                size="small"
                rows={3}
                onChange={(e) => this.onChange(e)}
                value={this.state.description}
                multiline
                fullWidth
              />
            </Grid>
          </Grid>

          <Box mb={2} />
          <Button
            variant="contained"
            color="secondary"
            size="large"
            type="submit"
            fullWidth>
            {t("upd")}
          </Button>
        </form>
      </>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn, user: state.profile.user };
};
export default connect(mapStateToProps, { getProfile, updateProfile })(
  withTranslation()(EditProfile)
);
