import React, { Component } from "react";
import { connect } from "react-redux";
import { Grid, Box, TextField, Button } from "@material-ui/core";

import { postquestion } from "../../../actions/profile";

export class Question extends Component {
  state = { question: "" };
  clearanswer = () => {
    this.setState({ question: "" });
  };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  onFormSubmit = async (event) => {
    event.preventDefault();
    this.props.postquestion(this.state.question, this.props.compid);
    this.props.regetqna();
  };
  render() {
    return (
      <form onSubmit={this.onFormSubmit} className="fullsize">
        <TextField
          name="question"
          color="secondary"
          className="fullsize answerclass"
          value={this.state.question}
          onChange={(e) => this.onChange(e)}
          autoComplete="off"
          placeholder={`Ask publicaly as ${this.props.user.fname} ${this.props.user.lname}`}
        />
        <Box mb={1} />
        <Grid container direction="row" alignItems="center">
          <Button
            variant="contained"
            color="secondary"
            type="submit"
            className="answerclass"
          >
            Submit
          </Button>
          <Button
            variant="outlined"
            color="secondary"
            onClick={this.clearanswer}
            className="answerclass"
          >
            Cancle
          </Button>
        </Grid>
      </form>
    );
  }
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps, { postquestion })(Question);
