import "./Auth.css";
import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import {
  TextField,
  Button,
  Box,
  Paper,
  Typography,
  Grid,
  Link,
} from "@material-ui/core";
import { baseURL } from "../../apis/api";
import { register } from "../../actions/auth";
import FacebookIcon from '@material-ui/icons/Facebook';
class Register extends Component {
  state = {
    fname: "",
    lname: "",
    email: "",
    password: "",
    cpassword: "",
    passmsg: "",
    cpassmsg: "",
  };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  onFormSubmit = (event) => {
    event.preventDefault();
    if (this.state.password !== this.state.cpassword) {
      this.setState({ cpassmsg: "* Password do not match" });
    } else {
      const { fname, lname, email, password } = this.state;
      this.props.register({ fname, lname, email, password });
    }
  };
  passLen = (e) => {
    this.onChange(e);
    if (e.target.value.length > 0 && e.target.value.length < 6) {
      this.setState({ passmsg: "* Password must be at least 6 characters." });
    } else {
      this.setState({ passmsg: "" });
    }

    if (this.state.cpassword) {
      if (this.state.password !== this.state.cpassword) {
        this.setState({ cpassmsg: "* Password do not match" });
      } else {
        this.setState({ cpassmsg: "" });
      }
    }
  };

  comPass = (e) => {
    this.onChange(e);
    if (this.state.password !== e.target.value) {
      this.setState({ cpassmsg: "* Password do not match" });
    } else {
      this.setState({ cpassmsg: "" });
    }
  };

  render() {
    const { t } = this.props;

    if (this.props.isSignedIn) {
      return <Redirect to="/updateprofile" />;
    }
    return (
      <div className="loginform register-w">
        <Paper variant="outlined" className="paperclass">
          <Typography variant="h4" className="center">
            Registeration
          </Typography>
          <Box mb={1} />
          <form onSubmit={this.onFormSubmit}>
            {/* First last and agency name */}
            <Grid container spacing={1}>
              <Grid item xs={12} md={6}>
                <TextField
                  name="fname"
                  label={t("fname")}
                  variant="outlined"
                  margin="normal"
                  color="secondary"
                  onChange={(e) => this.onChange(e)}
                  value={this.state.fname}
                  required
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  name="lname"
                  label={t("lname")}
                  variant="outlined"
                  margin="normal"
                  color="secondary"
                  onChange={(e) => this.onChange(e)}
                  value={this.state.lname}
                  required
                  fullWidth
                />
              </Grid>
            </Grid>

            {/* email */}
            <Grid container spacing={1}>
              <Grid item xs={12}>
                <TextField
                  name="email"
                  label="Email"
                  type="email"
                  variant="outlined"
                  margin="normal"
                  color="secondary"
                  onChange={(e) => this.passLen(e)}
                  value={this.state.email}
                  required
                  fullWidth
                />
              </Grid>
            </Grid>

            {/* password & confirm password */}
            <Grid container spacing={1}>
              <Grid item xs={12} md={6}>
                <TextField
                  name="password"
                  label="Password"
                  variant="outlined"
                  margin="normal"
                  type="password"
                  color="secondary"
                  className="passclass"
                  onChange={(e) => this.passLen(e)}
                  value={this.state.password}
                  helperText={this.state.passmsg}
                  required
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  name="cpassword"
                  label="Confirm Password"
                  variant="outlined"
                  margin="normal"
                  type="password"
                  color="secondary"
                  className="passclass"
                  onChange={(e) => this.comPass(e)}
                  value={this.state.cpassword}
                  helperText={this.state.cpassmsg}
                  required
                  fullWidth
                />
              </Grid>
            </Grid>

            <Box mb={2} />
            <Button
              variant="contained"
              color="secondary"
              size="large"
              type="submit"
              fullWidth>
              Register
            </Button>
          </form>
          <Box mb={3} />
          <Typography color="textSecondary" className="center divtextcen">
            <span>or continue with</span>
          </Typography>
          <Box mb={3} />
          <Grid container spacing={2}>
            {/* google btn */}
            <Grid item xs={12} md={6}>
              <Link
                onClick={(e) => e.preventDefault}
                href={baseURL + "agency/auth/google"}>
                <Button
                  variant="contained"
                  className="google-btn"
                  size="large"
                  fullWidth>
                  Google
                </Button>
              </Link>
            </Grid>
            {/* facebook btn */}
            <Grid item xs={12} md={6}>
              <Link
                onClick={(e) => e.preventDefault}
                href={baseURL + "agency/auth/facebook"}>
                <Button
                  variant="contained"
                  className="facebook-btn"
                  size="large"
                  fullWidth>
                  <FacebookIcon/>
                  Facebook
                </Button>
              </Link>
            </Grid>
          </Grid>
        </Paper>
        <Box mb={2} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn };
};

export default connect(mapStateToProps, { register })(
  withTranslation()(Register)
);
