export const getallPosts = () => {
  return async (dispatch) => {
    try {
      const res = await axios.get(baseURL + "posta/getallposts");
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      } else {
        dispatch({ type: GET_POST, payload: res.data.allpost });
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

