import { GET_SUB, GET_SUBID, LOGOUT } from "../actions/types";
const INITIAL_STATE = {};

const subReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_SUB:
      return { ...state, subs: action.payload };
    case GET_SUBID:
      return { ...state, usersub: action.payload };
    case LOGOUT:
      return {
        ...state,
        subs: null,
        usersub: null,
      };
    default:
      return state;
  }
};

export default subReducer;
