import {
  Box,
  TextField,
  Button,
  Container,
  Typography,
  Grid,
} from "@material-ui/core";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { deleteProfile } from "../../../actions/profile";

class DeleteProfile extends Component {
  state = { reason: "", email: "" };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.deleteProfile(this.state.email, this.state.reason);
  };
  render() {
    return (
      <div>
        <Box mb={5} />
        <Container maxWidth="sm">
          <Typography style={{ textAlign: "center" }} variant="h2">
            Delete Profile
          </Typography>
          <form onSubmit={this.onFormSubmit}>
            <TextField
              name="email"
              label="Email"
              type="email"
              variant="outlined"
              margin="normal"
              color="secondary"
              onChange={(e) => this.onChange(e)}
              value={this.state.email}
              required
              fullWidth
              autoFocus
            />
            <TextField
              name="reason"
              label="Reason"
              variant="outlined"
              margin="normal"
              color="secondary"
              rows={3}
              placeholder="Describe why you want to delete your account"
              value={this.state.reason}
              onChange={(e) => this.onChange(e)}
              multiline
              fullWidth
              required
            />
            <Grid container spacing={2}>
              <Grid item xs={12} md={6}>
                <Link to="/profile" className="link-class">
                  <Button
                    variant="contained"
                    size="large"
                    color="secondary"
                    type="submit"
                    fullWidth
                  >
                    Submit
                  </Button>
                </Link>
              </Grid>
              <Grid item xs={12} md={6}>
                <Link to="/profile" className="link-class">
                  <Button
                    variant="outlined"
                    size="large"
                    color="secondary"
                    fullWidth
                  >
                    Cancel
                  </Button>
                </Link>
              </Grid>
            </Grid>
          </form>
        </Container>
      </div>
    );
  }
}

export default connect(null, { deleteProfile })(DeleteProfile);
