import React, { Component } from "react";
import { connect } from "react-redux";
import { Typography, Button } from "@material-ui/core";
import { pinreview } from "../../../../actions/review";

class PinReview extends Component {
  state = { pined: false };
  componentDidMount() {
    this.setState({ pined: this.props.pined });
  }
  onCLick = async () => {
    await this.setState({ pined: !this.state.pined });
    this.props.pinreview(this.props.id, this.state.pined);
  };
  displayPin = () => {
    if (this.state.pined) {
      return <>Unpin</>;
    } else {
      return <>Pin</>;
    }
  };
  pinUnpin = () => {
    if (this.props.user && this.props.user.usertype !== "visitor") {
      return (
        <>
          <Button
            variant="contained"
            size="small"
            color="secondary"
            onClick={this.onCLick}
          >
            {this.displayPin()}
          </Button>
        </>
      );
    } else {
      return this.state.pined ? <Typography>Pined</Typography> : <></>;
    }
  };
  render() {
    return <> {this.pinUnpin()} </>;
  }
}
const mapStateToProps = (state) => {
  return {
    user: state.profile.user,
  };
};
export default connect(mapStateToProps, { pinreview })(PinReview);
