import "./Index.css";
import React, { Component } from "react";
import {
  Container,
  Grid,
  Typography,
  Paper,
  InputBase,
  IconButton,
  Divider,
  Box,
  Button,
  Tooltip,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import ClearRoundedIcon from "@material-ui/icons/ClearRounded";
import { connect } from "react-redux";
import travel0 from "../../images/travel0.jpg";

import { indexSearch } from "../../actions/front";
import { Redirect } from "react-router-dom";

class FirstSection extends Component {
  state = { img: travel0, company: "", city: "", search: false };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  clearCompany = () => {
    this.setState({ company: "" });
  };
  clearCity = () => {
    this.setState({ city: "" });
  };
  onFormSubmit = (event) => {
    event.preventDefault();
    if (this.state.company || this.state.city) {
      this.props.indexSearch({
        company: this.state.company,
        city: this.state.city,
      });
    }
    this.setState({ search: true });
  };
  render() {
    if (this.state.search) {
      return <Redirect to="/visitorhome" />;
    }
    return (
      <div className="backimg">
        <Container className="gridclass">
          <Grid
            container
            className="gridclass"
            justifyContent="center"
            alignItems="center"
          >
            <Grid item xs={12} md={6} className="headclass">
              <Typography variant="h2">
                <b>Explore all the offers without having to travel</b>
              </Typography>
              <Typography variant="h5">
                Where else, let us suggest the best options for you
              </Typography>
            </Grid>
            {/* Search section */}
            <Grid item xs={12} md={6}>
              <Box display="flex" justifyContent="center">
                <div className="formspace">
                  <form onSubmit={this.onFormSubmit} className="searchbarclass">
                    <Typography>
                      Check out the best deals available now.
                    </Typography>
                    <Box mb={2} />
                    {/* Agency */}
                    <Paper className="root">
                      <Box display="flex" className="centerclass root">
                        <Box>
                          <Typography>
                            <b>What</b>
                          </Typography>
                        </Box>
                        <Box className="inputclass">
                          <Divider
                            orientation="vertical"
                            className="dividerclass"
                          />
                        </Box>
                        <Box flexGrow={1} className="inputclass">
                          <InputBase
                            name="company"
                            value={this.state.company}
                            onChange={(e) => this.onChange(e)}
                            style={{ width: "100%" }}
                            autoComplete="off"
                            placeholder="Try travel agency, hotels"
                          />
                        </Box>
                        <Box pr={1}>
                          <Tooltip title="Clear">
                            <IconButton
                              onClick={this.clearCompany}
                              className="iconButtonclass"
                              aria-label="search"
                            >
                              <ClearRoundedIcon />
                            </IconButton>
                          </Tooltip>
                        </Box>
                      </Box>
                    </Paper>
                    <Box mb={2} />
                    {/* City */}
                    <Paper className="root">
                      <Box display="flex" className="centerclass root">
                        <Box>
                          <Typography>
                            <b>Where</b>
                          </Typography>
                        </Box>
                        <Box className="inputclass">
                          <Divider
                            orientation="vertical"
                            className="dividerclass"
                          />
                        </Box>
                        <Box flexGrow={1} className="inputclass">
                          <InputBase
                            name="city"
                            value={this.state.city}
                            onChange={(e) => this.onChange(e)}
                            style={{ width: "100%" }}
                            autoComplete="off"
                            placeholder="Type city name"
                          />
                        </Box>
                        <Box pr={1}>
                          <Tooltip title="Clear">
                            <IconButton
                              onClick={this.clearCity}
                              className="iconButtonclass"
                              aria-label="search"
                            >
                              <ClearRoundedIcon />
                            </IconButton>
                          </Tooltip>
                        </Box>
                      </Box>
                    </Paper>
                    <Box mb={2} />

                    <Button
                      type="submit"
                      variant="contained"
                      color="secondary"
                      endIcon={<SearchIcon />}
                    >
                      Submit
                    </Button>
                  </form>
                </div>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}

export default connect(null, { indexSearch })(FirstSection);
