import axios from "axios";
import {
  REGISTER_FAIL,
  LOGIN_SUCCESS,
  LOGIN_FAIL,
  LOGOUT,
  GOOGLE_LOGIN,
} from "./types";
import { baseURL, config } from "../apis/api";
import { setAlert } from "./alert";

export const register = (user) => {
  return async (dispatch) => {
    const body = JSON.stringify(user);
    try {
      const res = await axios.post(baseURL + "user/register", body, config);

      if (res.data.msg.length) {
        dispatch(setAlert(res.data.msg[0], "warning"));
      }
      if (res.data.error.length) {
        dispatch(setAlert(res.data.error[0], "error"));
      }
    } catch (err) {
      console.error("error: ", err);

      dispatch({ type: REGISTER_FAIL });
    }
  };
};

export const login = ({ email, password }) => {
  return async (dispatch) => {
    const body = JSON.stringify({ email, password });
    try {
      const res = await axios.post(baseURL + "user/login", body, config);

      if (res.data.msg.length) {
        dispatch({ type: LOGIN_SUCCESS, payload: res.data });
        dispatch(setAlert(res.data.msg[0], "success"));
      }
      if (res.data.error.length) {
        dispatch(setAlert(res.data.error[0], "error"));
      }
    } catch (err) {
      console.error("error: ", err);
      dispatch({ type: LOGIN_FAIL });
      dispatch(setAlert("Server error", "error"));
    }
  };
};

// logout User
export const logout = () => {
  return async (dispatch) => {
    // const res =
    await axios.get(baseURL + "user/logout");
    dispatch({ type: LOGOUT });
    dispatch(setAlert("You are logged out!", "success"));
  };
};

export const google = () => {
  return async (dispatch) => {
    try {
      dispatch({ type: GOOGLE_LOGIN });
    } catch (err) {
      console.error("error: ", err);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

