import axios from "axios";
import { baseURL, config } from "../apis/api";
import { setAlert } from "./alert";

export const uploadReview = (review, id) => {
  return async (dispatch) => {
    const body = JSON.stringify(review);
    try {
      const res = await axios.post(
        baseURL + "review/postreview/" + id,
        body,
        config
      );
      if (res.data.msg.length) {
        dispatch(setAlert(res.data.msg[0], "success"));
      }
      if (res.data.error.length) {
        dispatch(setAlert(res.data.error[0], "error"));
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

export const likereview = (id) => {
  return async (dispatch) => {
    try {
      const body = JSON.stringify({ id });
      const res = await axios.post(baseURL + "review/like", body, config);
      if (res.data.error.length > 0) {
        dispatch(setAlert(res.data.error[0], "error"));
      }
    } catch (error) {
      console.error(error);
      dispatch(setAlert("Something went wrong", "error"));
    }
  };
};

