import { Container, Grid } from "@material-ui/core";
import React, { Component } from "react";
import SubsCard from "./SubsCard";
import { getSubid, getSubs } from "../../actions/Subs";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";

class Subs extends Component {
  componentDidMount() {
    this.props.getSubs();
    this.props.getSubid();
  }

  mapSubs = () => {
    if (this.props.subs) {
      return (
        <Container>
          <Grid container direction="row" justifyContent="center" spacing={2}>
            {this.props.subs.map((sub) => (
              <Grid item key={sub._id} xs={12} md={6} lg={3}>
                <SubsCard
                  sub={sub}
                  color={sub._id === this.props.subid ? "#55c949" : "#bdbdbd"}
                />
              </Grid>
            ))}
          </Grid>
        </Container>
      );
    } else {
      return <CircularProgress />;
    }
  };
  render() {
    if (!this.props.isSignedIn) {
      return <Redirect to="/login" />;
    }
    return (
      <div>
        <Container>{this.mapSubs()}</Container>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    subs: state.subs.subs,
    subid: state.subs.usersub,
  };
};

export default connect(mapStateToProps, { getSubs, getSubid })(Subs);
