import React, { Component } from "react";
import {
  Dialog,
  DialogContent,
  DialogTitle,
  Divider,
  IconButton,
  TextField,
  Button,
  Grid,
  Tooltip,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

import { connect } from "react-redux";
import { postReport } from "../../../actions/report";

class Report extends Component {
  state = { report: "" };
  onChange = (e) => {
    this.setState({ ...this.state, [e.target.name]: e.target.value });
  };
  onFormSubmit = (event) => {
    event.preventDefault();
    this.props.postReport(this.state.report, this.props.pid);
    this.props.handleClose();
  };
  render() {
    return (
      <>
        <Dialog
          open={this.props.open}
          onClose={this.props.handleClose}
          aria-labelledby="form-dialog-title"
        >
          {/* Titel */}
          <Grid
            container
            display="row"
            justifyContent="space-between"
            alignItems="center"
          >
            <DialogTitle id="form-dialog-title">Report</DialogTitle>
            <Tooltip title="Close">
              <IconButton
                edge="start"
                onClick={this.props.handleClose}
                aria-label="close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
          </Grid>

          <Divider />
          <DialogContent>
            {/* show user details */}
            <form onSubmit={this.onFormSubmit}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    name="report"
                    color="primary"
                    placeholder="Please describe reason here"
                    label="Reason"
                    style={{ width: "100%" }}
                    value={this.state.report}
                    onChange={this.onChange}
                    autoComplete="off"
                    required
                  />
                </Grid>
                <Grid
                  item
                  xs={12}
                  container
                  direction="row"
                  justifyContent="center"
                  alignItems="center"
                >
                  <Button variant="contained" color="primary" type="submit">
                    report
                  </Button>
                </Grid>
              </Grid>
            </form>
          </DialogContent>
        </Dialog>
      </>
    );
  }
}

export default connect(null, { postReport })(Report);
