import React, { Component } from "react";
import { Link } from "react-router-dom";

import {
  Box,
  Typography,
  Grid,
  Avatar,
  Button,
  Container,
} from "@material-ui/core";

class HeaderInfo extends Component {
  drowbtn = () => {
    return (
      <Grid item xs={12} container direction="row">
        <Grid item xs={12} sm={6}>
          <Link to="/updateprofile" className="link-class">
            <Button
              variant="contained"
              color="secondary"
              className="mbclass mrclass"
              size="large"
              fullWidth
            >
              Update Profile
            </Button>
          </Link>
        </Grid>
        <Grid item xs={12} sm={6}>
          <Link to="/deleteprofile" className="link-class">
            <Button
              variant="outlined"
              color="secondary"
              className="mlclass"
              size="large"
              fullWidth
            >
              Delete Profile
            </Button>
          </Link>
        </Grid>
      </Grid>
    );
  };
  render() {
    return (
      <Container>
        <Grid container direction="row" alignItems="center">
          <Grid
            container
            direction="column"
            justifyContent="center"
            alignItems="center"
            item
            xs={12}
            md={3}
          >
            <Avatar
              alt="Remy Sharp"
              src={this.props.userProp.avatar}
              className="avatarclass"
            />
            <Box mb={2} />
          </Grid>

          <Grid
            container
            direction="column"
            justifyContent="center"
            className="detailsclass"
            item
            xs={12}
            md={7}
          >
            <Typography variant="h5" className="comnameclass">
              {this.props.userProp.companyname}
            </Typography>
            <Typography variant="h6">
              {this.props.userProp.fname} {this.props.userProp.lname}
            </Typography>
            <Typography>{this.props.userProp.email}</Typography>
            <Typography gutterBottom>{this.props.userProp.address}</Typography>
            <Typography variant="body2">
              {this.props.userProp.description}
            </Typography>
            <Box mb={2} />

            {this.drowbtn()}
          </Grid>
        </Grid>
      </Container>
    );
  }
}
export default HeaderInfo;
