import "./NavBar.css";
import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import {
  AppBar,
  Button,
  IconButton,
  Toolbar,
  Typography,
  Tooltip,
} from "@material-ui/core";
import { logout } from "../../actions/auth";
import HomeIcon from "@material-ui/icons/Home";
import PersonIcon from "@material-ui/icons/Person";
import SubscriptionsIcon from "@material-ui/icons/Subscriptions";
// custom code
import Lang from "../Lang";
class NavBar extends Component {
  logoutUser = () => {
    this.props.logout();
  };
  drawBrand = () => {
    if (this.props.user && this.props.user.usertype !== "visitor") {
      return (
        <Link to="/profile" style={{ textDecoration: "none", color: "#fff" }}>
          Brand
        </Link>
      );
    } else {
      return (
        <Link to="/" style={{ textDecoration: "none", color: "#fff" }}>
          Brand
        </Link>
      );
    }
  };
  drawhome = () => {
    if (this.props.user && this.props.user.usertype === "visitor") {
      return (
        <Link to="/visitorhome" className="link-class">
          <Tooltip title="Home">
            <IconButton color="inherit">
              <HomeIcon />
            </IconButton>
          </Tooltip>
        </Link>
      );
    } else {
      <></>;
    }
  };
  drowBtn = () => {
    if (this.props.isSignedIn) {
      return (
        <>
          {this.drawhome()}
          <Link to="/profile" className="link-class">
            <Tooltip title="My Profile">
              <IconButton color="inherit">
                <PersonIcon />
              </IconButton>
            </Tooltip>
          </Link>
          {this.props.user && this.props.user.usertype !== "visitor" ? (
            <Link to="/subs" className="link-class">
              <Tooltip title="Subscriptions">
                <IconButton color="inherit">
                  <SubscriptionsIcon />
                </IconButton>
              </Tooltip>
            </Link>
          ) : (
            <></>
          )}

          <Link to="/" className="link-class">
            <Button color="inherit" onClick={this.logoutUser}>
              Logout
            </Button>
          </Link>
        </>
      );
    } else {
      return (
        <>
          <Link to="/login" className="link-class">
            <Button color="inherit">Login</Button>
          </Link>
          <Link to="/register" className="link-class">
            <Button color="inherit">Register</Button>
          </Link>
        </>
      );
    }
  };
  render() {
    return (
      <AppBar position="static" className="appbar-class">
        <Toolbar>
          <Typography variant="h6" className="title">
            {this.drawBrand()}
          </Typography>
          {this.drowBtn()}

          <Lang />
        </Toolbar>
      </AppBar>
    );
  }
}

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn, user: state.profile.user };
};

export default connect(mapStateToProps, { logout })(NavBar);
