import "./App.css";
import { Router, Route, Switch } from "react-router-dom";
import { ThemeProvider, Container } from "@material-ui/core";

import NavBar from "./NavBar/NavBar";
import Alert from "./Alerts/Alerts";
import Index from "./Index/Index";
import Login from "./Auth/Login";
import Subs from "./Subs/Subs";
import Register from "./Auth/Register";
import Profile from "./Company/EditPrifile/Profile";
import CompanyHome from "./Company/Home/CompanyHome";
import VisitorHome from "./Visitors/Home/VisitorHome";
import AddPost from "./Company/AddPost/AddPost";
import ViewPost from "./Visitors/ViewPost/ViewPost";
import history from "../history";
import myTheme from "./theme";
import Player from "./Player/Player";
import EditPost from "./Company/AddPost/Editpost";
import VisitorView from "./Company/Home/VisitorView";
import DeleteProfile from "./Company/DeleteProfile/DeleteProfile";

function App() {
  return (
    <ThemeProvider theme={myTheme}>
      <Router history={history}>
        <NavBar />
        <Container>
          <Alert />
        </Container>
        <Switch>
          {/* ROTUES FOR BOTH */}
          <Route path="/" exact component={Index} />
          <Route path="/login" exact component={Login} />
          <Route path="/register" exact component={Register} />
          <Route path="/profile" exact component={CompanyHome} />
          <Route path="/updateprofile" exact component={Profile} />
          <Route path="/player" exact component={Player} />
          <Route path="/viewpost/:id" exact component={ViewPost} />
          <Route path="/deleteprofile" exact component={DeleteProfile} />
          {/* COMPANY ROUTES */}
          <Route path="/companyhome" exact component={CompanyHome} />
          <Route path="/addpost" exact component={AddPost} />
          <Route path="/editpost/:id" exact component={EditPost} />
          <Route path="/subs" exact component={Subs} />
          {/* VISITOR ROUTES */}
          <Route path="/visitorhome" exact component={VisitorHome} />
          <Route path="/companyprofile/:index" exact component={VisitorView} />
        </Switch>
      </Router>
    </ThemeProvider>
  );
}

export default App;
