import {
  LOGOUT,
  GET_PROFILE,
  GET_QNA,
  UPDATE_AVATAR,
  GET_WORKINGHOURS,
} from "../actions/types";
const INITIAL_STATE = {
  user: null,
  workingHours: null,
  qna: null,
};

const profileReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_PROFILE:
      return { ...state, user: action.payload };
    case GET_WORKINGHOURS:
      return { ...state, workingHours: action.payload };
    case GET_QNA:
      return { ...state, qna: action.payload };
    case UPDATE_AVATAR:
      return {
        ...state,
        profile: {
          ...state.profile,
          user: {
            ...state.user,
            avatar: action.payload,
          },
        },
      };
    case LOGOUT:
      return {
        ...state,
        user: null,
        workingHours: null,
        qna: null,
      };
    default:
      return state;
  }
};

export default profileReducer;
