import "./Viewpost.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { withTranslation } from "react-i18next";

import {
  likepost,
  unlikepost,
  deletepost,
  getClick,
} from "../../../actions/post";
import DisplayReview from "./reviews/DisplayReview";
import DrawMap from "./DrawMap";
import ResponsivePlayer from "../../Player/ResponsivePlayer";
import Review from "./reviews/Review";
import Report from "./Report";

import LocationOnOutlinedIcon from "@material-ui/icons/LocationOnOutlined";
import FavoriteBorderIcon from "@material-ui/icons/FavoriteBorder";
import ReportOutlinedIcon from "@material-ui/icons/ReportOutlined";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import FavoriteIcon from "@material-ui/icons/Favorite";
import MessageIcon from "@material-ui/icons/Message";
import PersonIcon from "@material-ui/icons/Person";

import LinkIcon from "@material-ui/icons/Link";
import {
  Card,
  CardMedia,
  ImageList,
  ImageListItem,
  CardContent,
  Typography,
  Divider,
  Grid,
  Button,
  Paper,
  Box,
  Avatar,
  Container,
  Tooltip,
} from "@material-ui/core";

class ViewPost extends Component {
  state = {
    city: "",
    country: "",
    weblink: "",
    price: 0,
    promo: "",
    desc: "",
    img: [],
    vid: "",
    likes: 0,
    liked: 0,
    user: null,
    id: null,
    reviews: [],
    mainimg: "",
    tempvid: 0,
    index: null,
    open: false,
  };
  componentDidMount() {
    this.props.posts.forEach(async (post, index) => {
      if (post._id === this.props.match.params.id) {
        await this.setState({
          price: post.price,
          desc: post.description,
          likes: post.likes.length,
          img: post.photos,
          vid: post.videolink,
          user: post.User,
          id: post._id,
          reviews: post.reviews,
          mainimg: post.photos[0],
          index: index,
          promo: post.promo,
          city: post.city,
          country: post.country,
          weblink: post.weblink,
        });
        if (this.props.user) {
          if (this.props.user.usertype === "visitor") {
            this.props.getClick(post._id);
          }
        } else {
          this.props.getClick(post._id);
        }
        if (this.state.likes) {
          post.likes.forEach((id) => {
            if (this.props.user && this.props.user.usertype) {
              if (id === this.props.user._id) {
                this.setState({
                  liked: 1,
                });
              }
            }
          });
        }
      }
    });
  }
  drowAgency = () => {
    if (this.state.user) {
      return (
        <>
          <Grid container direction="column" alignItems="center">
            <Avatar
              className="profpost"
              alt="Visitor pic"
              src={this.state.user.avatar}
            />
            <Box mb={2} />
            <Grid
              container
              item
              xs={12}
              md={6}
              direction="column"
              justifyContent="center"
              alignItems="center"
              className="detailsclass"
            >
              <Typography component="h2">
                {this.state.user.companyname}
              </Typography>
              <Typography color="textSecondary">
                {this.state.user.fname} {this.state.user.lname}
              </Typography>
              <Typography color="textSecondary">
                {this.state.user.email}
              </Typography>
            </Grid>
            <Box mb={2} />
            {this.drawvisitprofile()}
          </Grid>
          {this.drawReport()}
        </>
      );
    } else {
      return <></>;
    }
  };
  // Opens dialouge box
  handleClickOpen = () => {
    this.setState({ open: true });
  };
  // Close dialouge box
  handleClose = () => {
    this.setState({ open: false });
  };
  drawvisitprofile = () => {
    if (this.props.user && this.props.user.usertype === "visitor") {
      return (
        <>
          <Link
            to={`/companyprofile/${this.state.index}`}
            className="link-class"
          >
            <Button
              variant="contained"
              color="secondary"
              startIcon={<PersonIcon />}
            >
              Visit profile
            </Button>
          </Link>
          <Box mb={2} />
          <Divider style={{ width: "100%" }} />
        </>
      );
    }
  };
  drawReport = () => {
    if (this.props.user && this.props.user.usertype === "visitor") {
      return (
        <>
          <Box mb={2} />
          <Box display="flex" flexDirection="row" alignItems="center">
            <Box pr={1} pl={2}>
              <Tooltip title="Report">
                <ReportOutlinedIcon color="action" />
              </Tooltip>
            </Box>
            <Box pl={1} flexGrow={1}>
              <Typography>Want to report this? </Typography>
            </Box>
            <Box pr={3}>
              <Button
                variant="contained"
                color="primary"
                onClick={this.handleClickOpen}
              >
                Report
              </Button>
            </Box>
          </Box>
          <Report
            handleClose={this.handleClose}
            open={this.state.open}
            pid={this.state.id}
          />
        </>
      );
    }
  };
  like = () => {
    let temp = this.state.likes;
    if (this.state.liked === 1 && this.state.likes > 0) {
      --temp;
      this.setState({ liked: 0, likes: temp });
      this.props.unlikepost(this.state.id);
    } else {
      ++temp;
      this.setState({ liked: 1, likes: temp });
      this.props.likepost(this.state.id);
    }
  };
  setVideo = () => {
    this.setState({
      tempvid: 1,
    });
  };
  displaylikes = () => {
    if (this.state.liked === 0) {
      return <FavoriteBorderIcon className="likecolor" />;
    } else {
      return <FavoriteIcon className="likecolor" />;
    }
  };
  likebtn = () => {
    if (this.props.isSignedIn) {
      return (
        <Button className="cardfooter" onClick={this.like}>
          {this.displaylikes()}
          <Typography>{this.state.likes}</Typography>
        </Button>
      );
    } else {
      return (
        <Button className="cardfooter" disabled>
          {this.displaylikes()}
          <Typography>{this.state.likes}</Typography>
        </Button>
      );
    }
  };
  mainimg = () => {
    if (this.state.img.length > 0 && this.state.tempvid === 0) {
      return <CardMedia className="imgss" image={this.state.mainimg} />;
    } else {
      return <ResponsivePlayer url={this.state.vid} />;
    }
  };
  setimg = (image) => {
    this.setState({
      mainimg: image,
      tempvid: 0,
    });
  };
  drowback = () => {
    if (this.props.user && this.props.user.usertype) {
      return (
        <>
          <Link
            to={
              this.props.user.usertype === "visitor"
                ? "/visitorhome"
                : "/profile"
            }
            className="link-class"
          >
            <Button>
              <ArrowBackIosIcon /> Back
            </Button>
          </Link>
          <Box mb={2} />
        </>
      );
    } else {
      return (
        <>
          <Link to="/visitorhome" className="link-class">
            <Button>
              <ArrowBackIosIcon /> Back
            </Button>
          </Link>
          <Box mb={2} />
        </>
      );
    }
  };
  review = () => {
    if (this.props.user && this.props.user.usertype === "visitor") {
      return <Review postid={this.state.id} />;
    }
  };
  deletep = () => {
    if (this.state.id) {
      this.props.deletepost(this.state.id);
    }
  };
  drowUpdateDel = () => {
    if (this.props.user && this.props.user.usertype !== "visitor") {
      return (
        <>
          <Grid xs={0} md={1} />
          <Grid xs={12} md={4}>
            <Link to={`/editpost/${this.state.id}`} className="link-class">
              <Button variant="contained" color="secondary" fullWidth>
                Edit
              </Button>
            </Link>
            <Box mb={2} />
          </Grid>
          <Grid xs={0} md={1} />
          <Grid xs={12} md={4}>
            <Link to="/profile" className="link-class">
              <Button
                variant="outlined"
                color="secondary"
                onClick={this.deletep}
                fullWidth
              >
                Delete
              </Button>
            </Link>
            <Box mb={2} />
          </Grid>
        </>
      );
    } else {
      return <></>;
    }
  };
  promo = () => {
    if (this.state.promo) {
      return (
        <>
          <Divider />
          <Box display="flex" justifyContent="center" p={1}>
            <Box>
              <Typography variant="body1" component="p">
                Promo: <b>{this.state.promo}</b>
              </Typography>
            </Box>
          </Box>
        </>
      );
    } else {
      return <></>;
    }
  };
  webLink = () => {
    if (this.state.weblink) {
      return (
        <>
          <Box mb={1} />
          <Box display="flex" flexDirection="row">
            <Box pr={1} pl={2}>
              <Tooltip title="Website link">
                <LinkIcon color="action" />
              </Tooltip>
            </Box>
            <Box pl={1}>
              <a
                href={this.state.weblink}
                rel="noreferrer"
                target="_blank"
                className="weblinkclass"
              >
                <Typography>Visit my website</Typography>
              </a>
            </Box>
          </Box>
          <Box mb={1} />
          <Divider />
        </>
      );
    } else {
      return <></>;
    }
  };
  render() {
    return (
      <Container>
        <Box mb={2} />
        {/* back update delete btn */}
        <Grid container>
          <Grid item xs={12} md={1}>
            {this.drowback()}
          </Grid>
          {this.drowUpdateDel()}
        </Grid>

        <Grid container spacing={3}>
          <Grid item xs={12} md={8}>
            {/* Images */}
            <Card variant="outlined">
              {this.mainimg()}
              <Grid direction="row" container spacing={1}>
                <Grid className="rootimglist" item>
                  <ImageList className="imgList" cols={3}>
                    {this.state.img.map((image, index) => {
                      return (
                        <ImageListItem key={index}>
                          <CardMedia
                            component="img"
                            className="listimages"
                            alt="Contemplative Reptile"
                            image={image}
                            onClick={(event) => this.setimg(image)}
                          />
                        </ImageListItem>
                      );
                    })}
                    {this.state.vid !== "" ? (
                      <ImageListItem>
                        <video
                          src={this.state.vid}
                          autoPlay
                          loop
                          muted
                          onClick={this.setVideo}
                        >
                          Your browser does not support the video tag.
                        </video>
                      </ImageListItem>
                    ) : (
                      <></>
                    )}
                  </ImageList>
                </Grid>
              </Grid>
              <Divider />
              <Grid
                container
                direction="row"
                justifyContent="space-evenly"
                alignItems="center"
              >
                {this.likebtn()}
                <Divider orientation="vertical" flexItem />
                <Typography className="cardfooter">
                  <AttachMoneyIcon className="dollercolor" />
                  <Typography>{this.state.price}</Typography>
                </Typography>
              </Grid>
              {this.promo()}
            </Card>
            <Box mb={2} />
            {/* Description */}
            <Card variant="outlined">
              <CardContent>
                <Typography variant="body2" color="textSecondary" component="p">
                  {this.state.desc}
                </Typography>
              </CardContent>
            </Card>
            <Box mb={2} />
            {/* Add reivew */}
            {this.review()}
            <Box mb={2} />
            {/* Review heading */}
            <Box display="flex" alignItems="flex-end" justifyContent="center">
              <Box>
                <MessageIcon
                  style={{ fontSize: 40, marginRight: 15 }}
                  color="secondary"
                />
                <Divider />
              </Box>
              <Box>
                <Typography variant="h3" color="secondary">
                  Reviews
                </Typography>
                <Divider />
              </Box>
            </Box>

            <Box mb={2} />
            {/* Draw review */}
            <DisplayReview
              reviews={this.state.reviews}
              user={this.state.user}
            />
          </Grid>
          <Box mb={2} />

          {/* Map and Company det */}
          <Grid item xs={12} md={4}>
            <Paper variant="outlined">
              {/* Map */}
              <DrawMap />
              <Box mb={2} />
              {/* Place detail */}
              <Box display="flex" flexDirection="row">
                <Box pr={1} pl={2}>
                  <Tooltip title="Location">
                    <LocationOnOutlinedIcon color="action" />
                  </Tooltip>
                </Box>
                <Box pl={1}>
                  <Typography>
                    {this.state.city}, {this.state.country}
                  </Typography>
                </Box>
              </Box>
              <Box mb={1} />
              <Divider />
              {/* Weblink */}
              {this.webLink()}
              <Box mb={2} />
              {/* Show agency profile */}
              {this.drowAgency()}
              <Box mb={2} />
            </Paper>
            <Box mb={2} />
          </Grid>
        </Grid>
      </Container>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    posts: state.posts.posts,
    user: state.profile.user,
  };
};
export default connect(mapStateToProps, {
  likepost,
  unlikepost,
  deletepost,
  getClick,
})(withTranslation()(ViewPost));
