import "./CompanyHome.css";
import React, { Component } from "react";
import { Box, Paper, Grid, Fab } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import { Redirect } from "react-router";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { getSubid } from "../../../actions/Subs";
// components
import PostCard from "./PostCard";
import HeaderInfo from "./HeaderInfo";
import DisplayWorkingHour from "./DisplayWorkingHour";
import DisplayQNA from "./DisplayQNA";
import { getallPosts } from "../../../actions/post";

class CompanyHome extends Component {
  componentDidMount() {
    this.props.getallPosts();
    if (this.props.user && this.props.user.usertype !== "visitor") {
      this.props.getSubid();
    }
  }
  ifpost = () => {
    if (this.props.posts) {
      return this.props.posts.map((post) => {
        if (this.props.user._id === post.User._id) {
          return (
            <Grid item key={post.id} xs={12} md={6} lg={3}>
              <PostCard post={post} usertype={this.props.user.usertype} />
            </Grid>
          );
        }
        return <></>;
      });
    } else {
      return <></>;
    }
  };
  drowPosts = () => {
    if (this.props.user.usertype !== "visitor") {
      return (
        <>
          <Box mb={3} />
          <DisplayWorkingHour />
          <Box mb={3} />
          <DisplayQNA user={this.props.user} visitor={null} />
          <Box mb={3} />
          <Link to="/addpost" className="link-class">
            <Fab color="secondary" aria-label="add">
              <AddIcon />
            </Fab>
          </Link>
          <Box mb={3} />
          <Grid container spacing={2}>
            {this.ifpost()}
          </Grid>
        </>
      );
    }
  };
  render() {
    if (this.props.user && this.props.user.usertype) {
      return (
        <div className="divcenter companydetailscard">
          <Paper container="true" variant="outlined" className="paperclass">
            <HeaderInfo
              userProp={this.props.user}
              usertype={this.props.user.usertype}
            />
          </Paper>
          {this.drowPosts()}
          <Box mb={3} />
        </div>
      );
    } else {
      return <Redirect to="/updateprofile" />;
    }
  }
}
const mapStateToProps = (state) => {
  return { posts: state.posts.posts, user: state.profile.user };
};
export default connect(mapStateToProps, { getallPosts, getSubid })(
  withTranslation()(CompanyHome)
);
