import "./CompanyHome.css";
import React, { Component } from "react";
import { connect } from "react-redux";
import { withTranslation } from "react-i18next";

import {
  Accordion,
  AccordionSummary,
  Typography,
  TableContainer,
  Table,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
} from "@material-ui/core";

import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

class DisplayWorkingHour extends Component {
  drawWH = () => {
    const { t } = this.props;
    if (this.props.workingHours && this.props.workingHours.length > 0) {
      return (
        <TableContainer>
          <Table size="small" aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>{t("days")}</TableCell>
                <TableCell>{t("start")}</TableCell>
                <TableCell>{t("end")}</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {this.props.workingHours.map((wh) => {
                return (
                  <TableRow key={Object.keys(wh)[0]}>
                    <TableCell>{Object.keys(wh)[0]}</TableCell>
                    <TableCell>{wh[Object.keys(wh)][0]}</TableCell>
                    <TableCell>{wh[Object.keys(wh)][1]}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      );
    } else {
      return <> </>;
    }
  };
  render() {
    return (
      <div>
        <Accordion variant="outlined">
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>Working Hours</Typography>
          </AccordionSummary>
          <div className="workclass">{this.drawWH()}</div>
        </Accordion>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    workingHours: state.profile.workingHours,
  };
};

export default connect(mapStateToProps)(withTranslation()(DisplayWorkingHour));
